import { Component, OnInit } from '@angular/core';
import { User } from '../../models/user';
import { Status } from '../../models/status.model';
import { ConfirmFormService } from '../../services/confirmForm.service';
import { Store } from '@ngrx/store';
import { ConfirmForm } from '../../models/confirmForm.model';
import * as actions from '../../store/actions/status.actions';
import { AppState } from '../../store/app.reducers';


@Component({
  selector: 'app-firm',
  templateUrl: './firm.component.html',
  styles: ['']
})
export class FirmComponent implements OnInit {

  currentStatus: Status;
  activate = false;
  currentUser: User;
  confirmation: ConfirmForm;

  constructor(
    public confirmFormService: ConfirmFormService,
    private store: Store<AppState>
  ) { }

  ngOnInit(): void {
    this.confirmFormService.initializeFormGroup();

    this.store.select('status').subscribe(({ status }) => {
      if (status != null) {
        // console.log('el estado actual es ', status);
        this.currentStatus = status;
        if ( status.formId != null) {
          this.getConfirmForm( status.formId);
        } else {
          this.getConfirmForm( 'new');
        }
      }
    });
  }

  getConfirmForm(formId: string) {

    if ( formId === 'new' ) {
      console.log('confirmacion nueva');
      this.confirmFormService.initializeFormGroup();

    } else {
      // console.log('id formulario ', formId);
      this.confirmFormService.getConfirmation(formId).subscribe( confirmation => {
        console.log('confiracion cargada', confirmation);
        if ( confirmation ){
        this.confirmFormService.onSetForm(confirmation['confirmation']);
        }
      });
    }

  }

  onChange(value: boolean) {
    // console.log('value ', value);
    this.confirmFormService.form.controls['codeRequest'].setValue(this.currentStatus.codeRequest);
    this.confirmFormService.saveConfirmation(this.confirmFormService.form.value).subscribe( resp => {
      console.log('confirm form ', resp );
      const std: Status = Status.fromState( this.currentStatus );
      std.formId = resp.id;
      std.status = 'terminado';
      // console.log('current status ', std);
      this.store.dispatch(actions.updateStatus({ status: std }));
      // this.store.dispatch(actions.resetStatus());
    });
    // console.log('Valor del form ', this.confirmFormService.form.value.userId);
  }

  onSubmit() {

  }

}
