import { Pipe, PipeTransform } from '@angular/core';
import { Status } from '../../models/status.model';
import { validFilters } from '../../store/actions/statuss.actions';

@Pipe({
  name: 'estates'
})
export class EstatesPipe implements PipeTransform {

  transform(statuss: Status[], filter: validFilters ): Status[] {
    // console.log('Estado entrante ', filter);
    switch ( filter ) {
      case 'proceso':
        return statuss.filter( status => status.status === 'proceso');
      case 'pendiente':
        return statuss.filter( status => status.status === 'pendiente');
      case 'terminado':
        return statuss.filter( status => status.status === 'terminado');
      default:
        return statuss;
    }

  }

}
