import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { AcquisitionRequest } from 'src/app/models/acquisitionRequest.model';
import { AcquisitionRequestService } from 'src/app/services/acquisitionRequest/acquisition-request.service';
import { Product } from 'src/app/models/product.model';
import { AdmCondition } from 'src/app/models/admCondition.model';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { AppState } from '../../store/app.reducers';
import { Status } from '../../models/status.model';
import { StatusService } from '../../services/status/status.service';
import { IStatus } from '../../interfaces/interfaces';

@Component({
  selector: 'app-acquisition-request-view',
  templateUrl: './acquisition-request-view.component.html',
  styles: ['']
})
export class AcquisitionRequestViewComponent implements OnInit {

  // @Input() id: string;
  currentStatus: Status;
  tracer: IStatus [] = [];
  acquisitionRequest: AcquisitionRequest;
  products: Product;
  admConditions: AdmCondition;
  panelOpenState = true;
  displayedProducts: string[] = ['name', 'description', 'quantity', 'price'];
  displayedAdmConditions: string[] = ['priority', 'type', 'warranty'];

  constructor(
    public acquisitionRequestService: AcquisitionRequestService,
    private statusService: StatusService,
    public router: Router,
    private store: Store<AppState>
  ) {}

  ngOnInit(): void {
    this.store.select('status').subscribe(({ status }) => {
      if (status != null) {
        // console.log('el estado actual es ', status.codeRequest);
        this.currentStatus = status;
        if(status.codeRequest) {
          this.getAcquisitionRequest(status.codeRequest);
        }
        // this.getStatus(status.codeRequest);
      }
    });
    // console.log('ID ingresado', this.id);

  }

  getAcquisitionRequest(id: string){
    this.acquisitionRequestService.getACQRequest(id)
      .subscribe(acquisitionRequest => {
        if (!acquisitionRequest){
          return;
        }
        this.acquisitionRequest = acquisitionRequest;
        this.products = acquisitionRequest.products;
        this.admConditions = acquisitionRequest.admConditions;

      });
  }


  getStatus(codeRequest: string) {
    if (!codeRequest) { return; }
    this.statusService.getStatusByRequest(codeRequest).subscribe( tracer => {
      // console.log('traza ', tracer);
      this.tracer = tracer;
    } );
  }


}
