import { Component, OnInit, Inject } from '@angular/core';
import { AdmCondition } from 'src/app/models/admCondition.model';
import { AdmConditionService } from 'src/app/services/admCondition/adm-condition.service';
import { Router, ActivatedRoute } from '@angular/router';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { SnackService } from 'src/app/services/snack.service';

@Component({
  selector: 'app-adm-condition',
  templateUrl: './adm-condition.component.html',
  styleUrls: ['./adm-condition.component.scss']
})
export class AdmConditionComponent implements OnInit {

    admCondition: AdmCondition; // = new AdmCondition("","","","","","","","");
    public admConditionForm: FormGroup;
    requestId: string;
    admConditionId: string;
  constructor(
    public _admCondition: AdmConditionService,
    public router: Router,
    public activatedRoute: ActivatedRoute,
    private admConditionFormBuilder : FormBuilder,
    private dialogRef: MatDialogRef<AdmConditionComponent>,
    @Inject(MAT_DIALOG_DATA) public data: AdmConditionComponent,
    public snackService: SnackService,
  ) {   }

  ngOnInit(): void {
    if(this.data.admConditionId !== 'new'){
      this.getAdmCondition(this.data.admConditionId);
    }
    this.admConditionForm = this.admConditionFormBuilder.group({
      priority:[null, Validators.required],
      type:[null, Validators.required],
      warranty:[null, Validators.required],
      deliveryTime:[null, Validators.required],
      placeDelivery:[null, Validators.required],
      posibleProviders:[null, Validators.required],
      requestId:[this.data.requestId],

    });
  }

  getAdmCondition(id: string){
    this._admCondition.getAdmCondition(id)
      .subscribe(admCondition => {
        if(!admCondition){
          this.onCloseDialog();
        }
        const {priority, type, warranty, deliveryTime, placeDelivery, posibleProviders, requestId} = admCondition;
        this.admCondition = admCondition;
        this.admConditionForm.setValue({priority, type, warranty, deliveryTime, placeDelivery, posibleProviders, requestId});
      })
  }

  onCloseDialog(){
    this.dialogRef.close();
  }

  save() {

    const {priority} = this.admConditionForm.value;
    // console.log(this.admConditionForm.value);
    if ( this.admCondition ) {
      // update
      const data = {
        ...this.admConditionForm.value,
        id: this.admCondition.id,
        requestId: this.admCondition.requestId
      }
      this._admCondition.updateAdmCondition( data )
        .subscribe( resp => {
          this.snackService.success('Se ha actualizado correctamente la condicion administrativa ' + `${ priority }`);
          this.onCloseDialog();
        })

    } else {
      // create

      this._admCondition.createAdmCondition( this.admConditionForm.value )
          .subscribe( (resp: any) => {
            this.snackService.success('Se ha actualizado correctamente la condicion administrativa '+ `${priority }`);
            this.onCloseDialog();
        })
    }



  }

}
