import { createAction, props } from '@ngrx/store';
import { Status } from '../../models/status.model';

export type validFilters = 'proceso' |'pendiente' | 'terminado';

export const loadStatuss = createAction(
    '[Statuss] Load statuss',
    props<{ positionId: string, procedureId: string, userId: string }>()
    );
export const loadStatussSuccess = createAction(
    '[Statuss] Load statuss Success',
    props<{ statuss: Status[] }>()
);
export const loadStatussError = createAction(
    '[Statuss] Load statuss Error',
    props<{ payload: any }>()
);
export const setFilterStatus = createAction(
    '[Filter] Set filter',
    props<{ filter: validFilters }>()
    )