import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map, catchError } from 'rxjs/operators';
import { IUser, IConfirmForm } from '../interfaces/interfaces';
import { FormGroup, FormControl, Validators } from '@angular/forms';
// import * as _ from 'lodash';
import { User } from '../models/user';
import { GLOBAL } from '../config/global';
import { ConfirmForm } from '../models/confirmForm.model';

@Injectable({
  providedIn: 'root'
})

export class ConfirmFormService {

  constructor( private http: HttpClient) { this.getStorage(); }

  private URL = GLOBAL.uri;
  user: User;
  token: string;

  form: FormGroup = new FormGroup({
    id: new FormControl(null),
    userId: new FormControl('', Validators.required),
    codeRequest: new FormControl(''),
    dateAt: new FormControl('', Validators.required),
    observation: new FormControl('', Validators.email),
    refuse: new FormControl(false)
  });

  getStorage() {
    if ( localStorage.getItem('token')) {
      this.token = localStorage.getItem('token');
      this.user = JSON.parse(localStorage.getItem('user'));
    } else {
      this.token = '';
      this.user = null;
    }
  }

  initializeFormGroup() {
    this.form.setValue({
      id: null,
      userId: this.user.id,
      codeRequest: '',
      dateAt: new Date(),
      observation: '',
      refuse: null
    });
  }

  getConfirmations() {

    // return this.http.get<IUser[]>(this.URL + '/users').pipe(map(results => results['data']));
   try{
    return this.http.get<IConfirmForm[]>(this.URL + '/confirmations')
    .pipe(
        map( results => {
          return results['confirmations'];
          }
        )
      );

   } catch(err) {
     console.log('error ====> ', err );
   }
  }

  getConfirmation(id: string) {
    return this.http.get<IConfirmForm>(this.URL + '/confirmations/' + id);
  }

  onSetForm(confirmation) {
    this.form.setValue(confirmation);
  }

  saveConfirmation( confirmation: ConfirmForm) {

    return this.http.post( this.URL + '/confirmations/', confirmation).pipe(
      map( (resp: any) => {
        return resp['confirmation'];
        })
      );
  }

  updateConfirmation( confirmation: ConfirmForm ) {

    return this.http.put( this.URL + '/confirmations/' + confirmation.id , confirmation ).pipe(
        map( (resp: any)  => {
          return true;
        }));

  }

  deleteConfirmation( id: string ) {

    return this.http.delete( this.URL + '/confirmationss/' + id ).pipe(
      map( resp => {
        // alert('Usuario eliminado correctamente');
        return true;
      })
    );
  }

}
