import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { ProcessStart } from 'src/app/models/processStart.model';
import { GLOBAL } from '../../config/global';

@Injectable({
  providedIn: 'root'
})
export class ProcessStartService {
  private URL = GLOBAL.uri;
  constructor(private http: HttpClient) {}

  getProcessStarts() {
    let url = this.URL + '/processStarts';
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.processStarts;
      })
    );
  }

  getProcessStart(id: string) {
    let url = this.URL + '/processStarts/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.processStart;
      })
    );
  }

  getPSD(id: string) {
    let url = this.URL + '/processStarts/all/' + `${id}`;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.processStart;
      })
    );
  }

  createProcessStart(processStart: {category: string, startDate:Date, endDate:Date}){
    let url = this.URL + '/processStarts';
    return this.http.post( url, processStart);

  }

  updateProcessStart(processStart: ProcessStart){
    let url = this.URL + '/processStarts/'+ processStart.id;
    return this.http.put( url, processStart);
  }

  deleteProcessStart( id: string) {

    let url = this.URL + '/processStarts/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }


}
