import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { PurchaseOrder } from 'src/app/models/purchaseOrder.model';
import { GLOBAL } from '../../config/global';

@Injectable({
  providedIn: 'root'
})
export class PurchaseOrderService {
  private URL = GLOBAL.uri;
  constructor(private http: HttpClient) {}

  getPurchaseOrders() {
    let url = this.URL + '/purchaseOrders';
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.purchaseOrders;
      })
    );
  }

  getPurchaseOrder(id: string) {
    let url = this.URL + '/purchaseOrders/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.purchaseOrder;
      })
    );
  }

  getPOD(id: string) {
    let url = this.URL + '/purchaseOrders/all/' + `${id}`;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.purchaseOrder;
      })
    );
  }

  createPurchaseOrder(purchaseOrder: {code: string, description:string}){
    let url = this.URL + '/purchaseOrders';
    return this.http.post( url, purchaseOrder);

  }

  updatePurchaseOrder(purchaseOrder: PurchaseOrder){
    let url = this.URL + '/purchaseOrders/'+ purchaseOrder.id;
    return this.http.put( url, purchaseOrder);
  }

  deletePurchaseOrder( id: string) {

    let url = this.URL + '/purchaseOrders/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }


}
