import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidatorFn, FormControl } from '@angular/forms';
import { ProcessStart } from 'src/app/models/processStart.model';
import { ProcessStartService } from 'src/app/services/processStart/process-start.service';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { DocumentService } from 'src/app/services/document/document.service';
import { SnackService } from 'src/app/services/snack.service';
import { ModalFileUploadService } from 'src/app/components/modal-file-upload/modal-file-upload.service';
import { Document } from 'src/app/models/document.model';
import { DocumentComponent } from '../document/document.component';
import { AuthService } from '../../services/auth.service';
import * as actions from '../../store/actions/status.actions';
import { Status } from '../../models/status.model';
import { User } from '../../models/user';
import { Store } from '@ngrx/store';
import { AppState } from '../../store/app.reducers';
import { FilePreviewComponent } from '../file-preview/file-preview.component';
import { ModalFileUploadComponent } from 'src/app/components/modal-file-upload/modal-file-upload.component';
import { Router } from '@angular/router';

@Component({
  selector: 'app-process-start-form',
  templateUrl: './process-start-form.component.html',
  styleUrls: ['./process-start-form.component.scss']
})
export class ProcessStartFormComponent implements OnInit {

  codeRequest: string;
  currentStatus: Status;
  currentUser: User;

  public processStartForm: FormGroup;
  public processStart: ProcessStart;
  documents: Document[] = [];
  displayedDocuments: string[] = ['document','preview','name', 'edit', 'delete'];
  iconFile = '../assets/images/icon/add-file.png';
  error:any={isError:false,errorMessage:''};

  constructor(
    public _processStart: ProcessStartService,
    private authService: AuthService,
    private formBuilder: FormBuilder,
    private router: Router,
    private dialog: MatDialog,
    public _document: DocumentService,
    public snackService: SnackService,
    public modalUploadFileService: ModalFileUploadService,
    private store: Store<AppState>
  ) { }

  ngOnInit(): void {

    this.currentUser =  JSON.parse(this.authService.getUser()) ;
    this.processStartForm = this.formBuilder.group({
      category:[null, Validators.required],
      startDate:[null, Validators.required],
      endDate:[null, [Validators.required]],

    }, {validator: this.MustMatch( 'startDate', 'endDate')} );

    this.store.select('status').subscribe(({ status }) => {
          if (status != null) {
            // console.log('el estado actual es ', status);
            this.currentStatus = status;
            if ( status.formId != null) {
              this.getProcessStart( status.formId);
            } else {
              this.getProcessStart( 'new');
            }
          }
        });

  }



  getProcessStart(id: string){

    if (id === 'new'){
      return;
    }
    this._processStart.getPSD(id)
      .subscribe(processStart => {

        if ( processStart ){
        const {category, startDate, endDate} = processStart;
        this.processStart = processStart;
        this.documents = processStart.documents;
        this.processStartForm.setValue({category, startDate, endDate});
        }
      });
  }

  terminate() {
    if (this.currentStatus.procedureId) {
        const std: Status = Status.fromState( this.currentStatus );
        if (this.documents.length > 0) {
              std.userId = this.currentUser.id;
              std.status = 'terminado';
              // console.log('current status ', std);
              this.store.dispatch( actions.updateStatus({ status: std }));
            }else {
                std.status = 'pendiente';
            }
    }
  }

  save() {

    const {category} = this.processStartForm.value;

    if ( this.processStart ) {
      // update
      const data = {
        ...this.processStartForm.value,
        id: this.processStart.id
      };
      this._processStart.updateProcessStart( data )
        .subscribe( resp => {
          this.snackService.success('Se ha actualizado correctamente el Inicio de Proceso ' + `${ category }`);
          if (!this.currentStatus.procedureId) {
            this.router.navigateByUrl('/processStarts');
          } else {
            this.terminate();
          }
        });

    } else {
      // create

      this._processStart.createProcessStart( this.processStartForm.value )
          .subscribe( (resp: any) => {
            if (this.currentStatus.procedureId) {
                const std: Status = Status.fromState( this.currentStatus );
                std.formId = resp.processStart.id;
                std.userId = this.currentUser.id;
                std.status = 'pendiente';
                // console.log('current status ', std);
                this.store.dispatch( actions.updateStatus({ status: std }));
            }
            this.getProcessStart(resp.processStart.id);
            this.snackService.success('Se ha creado correctamente el Inicio de Proceso ' + `${category }`);

            // this.router.navigateByUrl(`/processStart/${ resp.processStart.id }`)
        });
    }



  }

  delete( id: string ) {

    this._processStart.deleteProcessStart( id )
            .subscribe ( () =>
            { this._processStart.getProcessStarts();
              this.snackService.warn('Se ha borrado correctamente el Inicio de Proceso ');
            });

  }


MustMatch(controlName: string, matchingControlName: string) {
  return (formGroup: FormGroup) => {
      const control = formGroup.controls[controlName];
      const matchingControl = formGroup.controls[matchingControlName];

      if (matchingControl.errors && !matchingControl.errors.mustMatch) {
          // return if another validator has already found an error on the matchingControl
          return;
      }

      // set error on matchingControl if validation fails
      if (control.value > matchingControl.value) {
          matchingControl.setErrors({ mustMatch: true });
      } else {
          matchingControl.setErrors(null);
      }
  }
}

// compareTwoDates(){
//    if(new Date(this.processStartForm.controls['endDate'].value)<new Date(this.processStartForm.controls['startDate'].value)){
//       return null;
//    }
//    else{
//      return true;
//    }
// }

  //document
  getDocuments(){
    this._document.getDocuments()
      .subscribe(documents => this.documents = documents);
  }
  openAddDocumentDialog(){
    const documentDialogRef = this.dialog.open(DocumentComponent, {
      data: { formId: this.processStart.id,
              documentId: 'new'
      }
    });
    documentDialogRef.afterClosed().subscribe( result => {

      this.getProcessStart(this.processStart.id);
      // this.sendEvent();

    });
  }
  openUpdateDocumentDialog(id: string){
    const documentDialogRef = this.dialog.open(DocumentComponent, {
      data: { documentId: id}
    });
    documentDialogRef.afterClosed().subscribe( result => {
      this.getProcessStart(this.processStart.id);
      console.log('Documentos cargados', this.documents);
      this.terminate();
    });
  }

  openPreviewDocumentDialog(src: string){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.width = '100%';
    dialogConfig.data = {src: src};
    const documentDialogRef = this.dialog.open(FilePreviewComponent, dialogConfig);

  }

  deleteDocument( id: string ) {

    this._document.deleteDocument( id )
            .subscribe ( () =>
            { this._document.getDocuments();
              this.snackService.warn('Se ha borrado correctamente el documento ');
              this.getProcessStart(this.processStart.id);
              console.log('Documentos cargados', this.documents);
              this.terminate();
            });

  }

  /* sendEvent() {
    // console.log('eniva evento');
    this.eventRefresh.emit(true);
  } */

  createDocument(){
    const document: Document = {id:'',name:"Inicio Proceso",type: "", formId:this.processStart.id,src:"./uploads", status: "active", createDate: new Date()};
    this._document.createDocument(document)
      .subscribe(() =>
      {
        this._document.getDocuments();
        this.snackService.success('Se ha creado correctamente el documento ');
        this.getProcessStart(this.processStart.id);

      }
      );
  }

  changeFile(id: string) {
    this.dialog.open(ModalFileUploadComponent);
    this.modalUploadFileService.showModal( 'docs', id );

    this.modalUploadFileService.modalNotification
                .subscribe( (resp: any) => {
                  // console.log(resp);

                  this.changeIcon(resp.document.src);
                  this.getProcessStart(this.processStart.id);
                  this.terminate();
                });
  }

  changeIcon(src) {

    if (src == "./uploads") {
      this.iconFile = '../assets/images/icon/add-file.png';
      return;
    }
    const ext = src.split('.');
    // console.log('extencion', ext);

    if (ext[1] === 'pdf') {
      this.iconFile = '../assets/images/icon/pdf.png';
    }

  }

}
