import { Component, OnInit } from '@angular/core';
import {AcquisitionRequest} from '../../models/acquisitionRequest.model';
import { AcquisitionRequestService } from 'src/app/services/acquisitionRequest/acquisition-request.service';
import { SnackService } from 'src/app/services/snack.service';
import { Store } from '@ngrx/store';
import { AppState } from '../../store/app.reducers';
import { changeFormId } from '../../store/actions/status.actions';
import { Router } from '@angular/router';


@Component({
  selector: 'app-acquisition-request',
  templateUrl: './acquisition-request.component.html',
  styleUrls: ['./acquisition-request.component.scss']
})
export class AcquisitionRequestComponent implements OnInit {

  acquisitionRequests: AcquisitionRequest[] = [];
  displayedColumns: string[] = ['code', 'version', 'validity', 'numRequest', 'edit', 'delete'];
  constructor(
    public _acquisitionRequest: AcquisitionRequestService,
    public snackService: SnackService,
    private store: Store<AppState>,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.getAcquisitionRequests();
  }

  getAcquisitionRequests(){
    
    this._acquisitionRequest.getAcquisitionRequests()
      .subscribe(acquisitionRequests => this.acquisitionRequests = acquisitionRequests);
      // console.log('entro', this.acquisitionRequests);
  }

  edit(id: string) {
    this.store.dispatch( changeFormId({ formId: id}) );
    this.router.navigateByUrl('/acquisitionRequest/edit')
  }
  delete( id: string ) {

    this._acquisitionRequest.deleteAcquisitionRequest( id )
            .subscribe ( () =>
            {
              this.snackService.warn('Se ha borrado correctamente la Solicitud de Adquisicion ');
              this.getAcquisitionRequests();
            });

  }


}
