import { ProcedureFormComponent } from './procedure/procedure-form.component';
import { StatusComponent } from './status/status.component';
import { ProposalEvaluationsComponent } from './proposal-evaluations/proposal-evaluations.component';
import { ProposalEvaluationFormComponent } from './proposal-evaluations/proposal-evaluation-form.component';
import { PurchaseOrderComponent } from './purchase-order/purchase-order.component';
import { PurchaseOrderFormComponent } from './purchase-order/purchase-order-form.component';
import { ProposalComparisonComponent } from './proposal-comparison/proposal-comparison.component';
