import { LegalContractComponent } from './legal-contract/legal-contract.component';
import { LegalContractFormComponent } from './legal-contract/legal-contract-form.component';
import { PerformedComponent } from './performed/performed.component';
import { RoleGuard } from './guards/role.guard';




export const pagesRoutes: Routes = [
    { path: 'dashboard', component: DashboardComponent, data: {titulo: 'Dashboard'} },
    { path: 'users', component: UsersComponent, data: {titulo: 'Mantenimiento de usuarios'}  },
    { path: 'technicalReports', component: TechnicalReportComponent, data: {titulo: 'Informe Tecnico'}  },
    { path: 'technicalReport/:id', component: TechnicalReportFormComponent, data: {titulo: 'Formulario Informe Tecnico'}  },
    { path: 'acquisitionRequests', component: AcquisitionRequestComponent, data: {titulo: 'Solicitudes de Adquisicion'}  },
    { path: 'acquisitionRequest/:id', component: AcquisitionRequestFormComponent, data: {titulo: 'Formulario Solicitud de Adquisicion'}  },
    { path: 'budgetCertifications', component: BudgetCertificationComponent, data: {titulo: 'Certificacion Presupuestaria'}  },
    { path: 'budgetCertification/:id', component: BudgetCertificationFormComponent, data: {titulo: 'Formulario Certificacion Presupuestaria'}  },
    { path: 'companies', component: CompanyComponent, data: {titulo: 'Empresa'}  },
    { path: 'company/:id', component: CompanyFormComponent, data: {titulo: 'Empresa'}  },
    { path: 'offices', component: OfficeComponent, canActivate: [RoleGuard], data: {titulo: 'Unidad'}  },
    { path: 'office/:id', component: OfficeFormComponent, canActivate: [RoleGuard], data: {titulo: 'Formulario Unidad'}  },
    { path: 'procedures', component: ProcedureComponent, data: {titulo: 'Proceso'}  },
    { path: 'procedure/:id', component: ProcedureFormComponent, data: {titulo: 'Formulario Proceso'}  },
    { path: 'status', component: StatusComponent, data: {titulo: 'Estados'}  },
    { path: 'proposalEvaluations', component: ProposalEvaluationsComponent, data: {titulo: 'Evaluacion Propuesta'}  },
    { path: 'proposalEvaluation/:id', component: ProposalEvaluationFormComponent, data: {titulo: 'Formulario Evaluacion Propuesta'}  },
    { path: 'purchaseOrders', component: PurchaseOrderComponent, data: {titulo: 'Orden Compra'}  },
    { path: 'purchaseOrder/:id', component: PurchaseOrderFormComponent, data: {titulo: 'Formulario Orden Compra'}  },
    { path: 'proposalComparisons', component: ProposalComparisonComponent, data: {titulo: 'Comparacion Propuesta'}  },
    { path: 'proposalComparison/:id', component: ProposalComparisonFormComponent, data: {titulo: 'Formulario Comparacion Propuesta'}  },
    { path: 'processStarts', component: ProcessStartComponent, data: {titulo: 'Inicio Proceso'}  },
    { path: 'processStart/:id', component: ProcessStartFormComponent, data: {titulo: 'Formulario Inicio Proceso'}  },
    { path: 'technicalEvaluations', component: TechnicalEvaluationComponent, data: {titulo: 'Evaluacion Tecnica'}  },
    { path: 'technicalEvaluation/:id', component: TechnicalEvaluationFormComponent, data: {titulo: 'Formulario Evaluacion Tecnica'}  },
    { path: 'legalContracts', component: LegalContractComponent, data: {titulo: 'Contrato Legal'}  },
    { path: 'legalContract/:id', component: LegalContractFormComponent, data: {titulo: 'Formulario Contrato Legal'}  },
    { path: 'performed', component: PerformedComponent, data: {titulo: 'Historial de actividades'}  },
    { path: '', redirectTo: 'dashboard', pathMatch: 'full' }
];
