import { Component, OnInit } from '@angular/core';
import { Company } from 'src/app/models/company.model';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CompanyService } from 'src/app/services/company/company.service';
import { Router, ActivatedRoute } from '@angular/router';
import { MatDialogRef, MatDialog } from '@angular/material/dialog';
import { OfficeComponent } from '../office/office.component';
import { OfficeFormComponent } from '../office/office-form.component';
import { Office } from 'src/app/models/office.model';
import { OfficeService } from 'src/app/services/office/office.service';
import { SnackService } from 'src/app/services/snack.service';

@Component({
  selector: 'app-company-form',
  templateUrl: './company-form.component.html',
  styleUrls: ['./company-form.component.scss']
})
export class CompanyFormComponent implements OnInit {

  company: Company;
  public companyForm: FormGroup;
  offices: Office[] = [];
  displayedOffices: string[] = ['name','supOfficeId','edit', 'delete'];
  constructor(
    public _company: CompanyService,
    public router: Router,
    public activatedRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    public _office: OfficeService,
    public snackService: SnackService

  ) { }

  ngOnInit(): void {
    this.activatedRoute.params
    .subscribe( ({ id }) => this.getCompany( id ) );

    this.companyForm = this.formBuilder.group({
          name:[null, Validators.required],
          direction:[null, Validators.required],
          nit:[null, Validators.required],
          email:[null, Validators.required],
          phone:[null, Validators.required],
        });
    this.getOffices();
  }

  getCompany(id: string){
    if(id==='new'){
      return;
    }
    this._company.getCompany(id)
      .subscribe(company => {
        if(!company){
          return this.router.navigateByUrl(`/companies`);
        }
        const {name,direction,nit,email,phone} = company;
        this.company = company;
        this.companyForm.setValue({name,direction,nit,email,phone});
      })
  }

  save() {

    const {name} = this.companyForm.value;

    if ( this.company ) {
      // update
      const data = {
        ...this.companyForm.value,
        id: this.company.id
      }
      this._company.updateCompany( data )
        .subscribe( resp => {
          this.snackService.success('Se ha actualizado correctamente la empresa ' + `${ name }`);
        })

    } else {
      // create

      this._company.createCompany( this.companyForm.value )
          .subscribe( (resp: any) => {
            this.snackService.success('Se ha creado correctamente la empresa '+ `${name }`);
            this.router.navigateByUrl(`/company/${ resp.company.id }`)
        })
    }

  }

  getOffices(){
    this._office.getOffices()
      .subscribe(offices => this.offices = offices);
  }

  delete( id: string ) {

    this._office.deleteOffice( id )
            .subscribe ( () =>
            {
              this.snackService.warn('Se ha borrado correctamente a la Unidad');
              this.getOffices();
            });

  }



}
