import { Component, OnInit, Input } from '@angular/core';
import { ProposalProvider } from 'src/app/models/proposalProvider.model';
import { ProposalProviderService } from 'src/app/services/proposalProvider/proposal-provider.service';
import { SnackService } from 'src/app/services/snack.service';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { ModalFileUploadComponent } from 'src/app/components/modal-file-upload/modal-file-upload.component';
import { ModalFileUploadService } from 'src/app/components/modal-file-upload/modal-file-upload.service';
import { ProposalProviderFormComponent } from './proposal-provider-form.component';
import { DocumentService } from 'src/app/services/document/document.service';
import { Document } from 'src/app/models/document.model';
import { FilePreviewComponent } from '../file-preview/file-preview.component';
import { DocumentComponent } from '../document/document.component';
import { Status } from '../../models/status.model';
import { User } from '../../models/user';
import { AuthService } from '../../services/auth.service';
import * as actions from '../../store/actions/status.actions';
import { Store } from '@ngrx/store';
import { AppState } from '../../store/app.reducers';

@Component({
  selector: 'app-proposal-provider',
  templateUrl: './proposal-provider.component.html',
  styleUrls: ['./proposal-provider.component.scss']
})
export class ProposalProviderComponent implements OnInit {

  // @Input() id: string;
  id: string;
  proposalProviders: ProposalProvider[] = [];
  proposalProvidersDoc: ProposalProvider[] = [];
  displayedColumns: string[] = ['document', 'preview', 'name', 'nit', 'edit', 'delete'];
  iconFile = '../assets/images/icon/add-file.png';
  document: Document;
  panelOpenState = false;
  documents: Document[] = [];
  displayedDocuments: string[] = ['document','preview','name', 'edit', 'delete'];
  formId: string;
  currentStatus: Status;
  currentUser: User;

  constructor(
    public _proposalProvider: ProposalProviderService,
    private authService: AuthService,
    public snackService: SnackService,
    private dialog: MatDialog,
    public _document: DocumentService,
    public modalUploadFileService: ModalFileUploadService,
    private store: Store<AppState>
  ) { }

  ngOnInit(): void {
    this.currentUser =  JSON.parse(this.authService.getUser());
    // if (this.id)
    // {
    //   this.getProposalProviders(this.id);

    // }
    this.store.select('status').subscribe(({ status }) => {
      if (status != null) {
        // console.log('el estado actual es ', status);
        this.currentStatus = status;
        if ( status.formId != null) {
          this.id = status.formId;
          this.getProposalProviders( status.formId);
        }
      }
    });


  }

  getProposalProviders(id: string){
    this._proposalProvider.getPPForm(id)
      .subscribe(proposalProviders => {

        this.proposalProviders = proposalProviders;
        this.getDocuments();
      });


  }

  terminate() {
    // console.log('Documentos cargados', this.document);
    if (this.currentStatus.procedureId) {
      const std: Status = Status.fromState( this.currentStatus );
      std.userId = this.currentUser.id;
      std.status = 'terminado';
      // console.log('current status ', std);
      this.store.dispatch( actions.updateStatus({ status: std }));
    }
  }

  delete( id: string ) {

    this._proposalProvider.deleteProposalProvider( id )
            .subscribe ( () =>
            {
              this.snackService.warn('Se ha borrado correctamente el Proveedor ');
              this.getProposalProviders(this.id);
            });

  }

  openAddProposalProviderDialog(){
    const proposalProviderDialogRef = this.dialog.open(ProposalProviderFormComponent, {
      data: { formId: this.id,
              proposalProviderId: 'new'
      }
    });
    proposalProviderDialogRef.afterClosed().subscribe( result => {
      this.getProposalProviders(this.id);
      // console.log('Documentos cargados', this.document);
      this.terminate();
    });

  }

  openUpdateProposalProviderDialog(id: string){
    const proposalProviderDialogRef = this.dialog.open(ProposalProviderFormComponent, {
      data: {
              proposalProviderId: id
      }
    });
    proposalProviderDialogRef.afterClosed().subscribe( result => {
      this.getProposalProviders(this.id);
      this.terminate();
    });

  }

  /* openPreviewDocument(src: string){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.width = '100%';
    dialogConfig.data = {src};
    const documentDialogRef = this.dialog.open(FilePreviewComponent, dialogConfig);

  }

  openPreviewDocumentDialog(id: string){
    this._document.getDocumentForm(id).subscribe(document => {
      this.document = document;
      this.openPreviewDocument(this.document.src);
    });
  } */
  getDocuments(){
    this._document.getDocuments().subscribe(documents => {
      this.documents = documents;
    })
  }
  getDocumentsProviders(id:string){
    this._document.getDocumentForm(id).subscribe(documents => {
      this.documents = documents;
    })
  }
  openUpdateDocumentDialog(id: string){
    const documentDialogRef = this.dialog.open(DocumentComponent, {
      data: { documentId: id}
    });
    documentDialogRef.afterClosed().subscribe( result => {
      this.getDocuments();
      this.terminate();
    });
  }

  openPreviewDocumentDialog(src: string){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.width = '100%';
    dialogConfig.data = {src: src};
    const documentDialogRef = this.dialog.open(FilePreviewComponent, dialogConfig);

  }

  deleteDocument( document: Document ) {
    document.status = 'inactive';
    this._document.updateDocument( document )
            .subscribe ( () =>
            { this.getDocuments();
              this.snackService.warn('Se ha borrado correctamente el documento ');

            });

  }

  createDocument(id: string){
    const date = new Date();
    const document: Document = {id:'',name:"Documento Proveedor",type: "", formId: id,src:"./uploads", status:"active", createDate: date};
    this._document.createDocument(document)
      .subscribe( (resp: any) =>
      {
        this.getDocuments();
        this.snackService.success('Se ha creado correctamente el documento ');

      })
  }

  createDocumentReplace(id:string){
    const documentR: Document = {id:'', name:"Documento Proveedor",type: "", formId: id, src:"./uploads", status:"active", createDate: new Date};
    this._document.createDocument(documentR)
    .subscribe( (resp: any) =>
    {
      console.log(resp.document);
      this.changeFile(resp.document.id);



    })
  }

  updateStatusDocument(document: Document){
    document.status="replaced";
    this.formId=document.formId;
    this._document.updateDocument(document)
      .subscribe((resp:any) => {
        console.log(resp.document.name);
        this.createDocumentReplace(this.formId);

      })
  }

  replaceFile(document: Document){
    this.updateStatusDocument(document);

  }

  changeFile(id: string) {
    this.dialog.open(ModalFileUploadComponent);
    this.modalUploadFileService.showModal( 'docs', id );
    this.modalUploadFileService.modalNotification
                .subscribe( (resp: any) => {
                  // console.log( resp);
                  this.snackService.success('Se ha modificado correctamente el adjunto del documento ');
                  this.changeIcon(resp.document.src);
                  this.getDocuments();
                  //this.sendEvent();
                  this.terminate();
                });
  }

  changeIcon(src) {

    if (src == "./uploads") {
      this.iconFile = '../assets/images/icon/add-file.png';
      return;
    }
    const ext = src.split('.');
    // console.log('extencion', ext);

    if (ext[1] === 'pdf') {
      this.iconFile = '../assets/images/icon/pdf.png';
    }

  }




}
