import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { Company } from 'src/app/models/company.model';
import { GLOBAL } from '../../config/global';

@Injectable({
  providedIn: 'root'
})
export class CompanyService {
  private URL = GLOBAL.uri;
  constructor(private http: HttpClient) { }

  getCompanies() {
    let url = this.URL + '/companies';
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.companies;
      })
    );
  }

  getCompany(id: string) {
    let url = this.URL + '/companies/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.company;
      })
    );
  }

  createCompany(company: {name: string, direction: string, nit: string, email: string, phone:string}){
    let url = this.URL + '/companies';
    return this.http.post( url, company);

  }

  updateCompany(company: Company){
    let url = this.URL + '/companies/'+company.id;
    return this.http.put( url, company);
  }

  deleteCompany( id: string) {

    let url = this.URL + '/companies/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }


}
