import { Component, OnInit } from '@angular/core';
import { ProposalComparison } from 'src/app/models/proposalComparison.model';
import { ProposalComparisonService } from 'src/app/services/proposalComparison/proposal-comparison.service';
import { SnackService } from 'src/app/services/snack.service';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { AppState } from 'src/app/store/app.reducers';
import * as actions from '../../store/actions/status.actions';

@Component({
  selector: 'app-proposal-comparison',
  templateUrl: './proposal-comparison.component.html',
  styleUrls: ['./proposal-comparison.component.scss']
})
export class ProposalComparisonComponent implements OnInit {

  proposalComparisons: ProposalComparison[] = [];
  displayedColumns: string[] = ['code','description','edit','delete'];

  constructor(
    public _proposalComparison: ProposalComparisonService,
    public snackService: SnackService,
    private router: Router,
    private store: Store<AppState>
  ) { }

  ngOnInit(): void {
    this.getProposalComparisons();
  }

  getProposalComparisons(){
    this._proposalComparison.getProposalComparisons()
      .subscribe(proposalComparisons => this.proposalComparisons = proposalComparisons);
  }

  edit(id: string){
    this.store.dispatch( actions.changeFormId({ formId: id}) );
    this.router.navigateByUrl('/proposalComparison/edit');
  }

  delete(id: string){
    this._proposalComparison.deleteProposalComparison(id)
      .subscribe(() =>
      {
        this.snackService.warn('Se ha borrado correctamente la Propuesta Comparacion ');
        this.getProposalComparisons();
      })
  }

}
