import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { AppState } from '../../../store/app.reducers';
import { Status } from '../../../models/status.model';
import * as actions from '../../../store/actions/status.actions';
import * as actionss from '../../../store/actions/statuss.actions';
import { ProcedureService } from '../../../services/procedure/procedure.service';
import { User } from '../../../models/user';
import { AuthService } from '../../../services/auth.service';

@Component({
  selector: 'app-pursue',
  templateUrl: './pursue.component.html',
  styleUrls: ['./pursue.component.scss']
})
export class PursueComponent implements OnInit {

  currentStatus: Status;
  activate = false;
  currentUser: User;
  constructor(
          private store: Store<AppState>,
          private procedureService: ProcedureService,
          private authService: AuthService
  ) {}

  ngOnInit(): void {
      this.store.select('status').subscribe(({status}) => {
        if (status != null) {

          // console.log('el estado actual es ', status);
          this.currentStatus = status;
          if (status.status === 'terminado') {
            this.switchButtons(true);
            }else {
              this.switchButtons(false);
            }
          }
        });
  }

  switchButtons(active: boolean) {
    this.activate = active;
  }

  nextTask() {
    // console.log('press next');
    this.currentUser =  JSON.parse(this.authService.getUser()) ;
    const std: Status = Status.fromState( this.currentStatus );
    std.current = false;
            // console.log('current status ', std);
    this.store.dispatch( actions.updateStatus({ status: std }));

    this.procedureService.getProcedure(std.procedureId).subscribe( currentProcedure => {
        const nextOrder = currentProcedure.order + 1;
        this.procedureService.getProcedureByOrder(nextOrder).subscribe( procedure => {
          const nextstd: Status = Status.fromState( std );
          nextstd.procedureId = procedure.id;
          nextstd.name = procedure.procedureName;
          nextstd.userId = null;
          nextstd.formId = null;
          nextstd.status = 'proceso';
          nextstd.dateAt = new Date();
          nextstd.current = true;
          // para cargar correctamente la lista de tareas
          const currentPID = this.currentStatus.procedureId;
          // Crear estado y actualizar lista de tareas
          this.store.dispatch( actions.createStatus({status: nextstd}));
          this.store.dispatch( actionss.loadStatuss(
            {
              positionId: this.currentUser.positionId,
              procedureId: currentPID,
              userId: this.currentUser.id
            }));

          this.store.dispatch( actions.resetStatus());
        });
    });
  }

  refuseTask() {

    console.log('press refuse');
  }
 

}
