import { Component, OnInit } from '@angular/core';
import { Procedure } from 'src/app/models/procedure.model';
import { ProcedureService } from 'src/app/services/procedure/procedure.service';
import { SnackService } from 'src/app/services/snack.service';
import { PositionService } from 'src/app/services/position/position.service';
import { Position } from 'src/app/models/position.model';

@Component({
  selector: 'app-procedure',
  templateUrl: './procedure.component.html',
  styleUrls: ['./procedure.component.scss']
})
export class ProcedureComponent implements OnInit {

  procedures: Procedure [] = [];
  displayedColumns: string[] = ['positionId', 'procedureName', 'order', 'edit', 'delete'];
  positions: Position [] = [];
  position: Position;
  name: string;

  constructor(
    public _procedure: ProcedureService,
    public snackService: SnackService,
    public _position: PositionService,
  ) { }


  ngOnInit(): void {
    this.getProcedures();
    this.getPositions();
  }
  getProcedures(){
    this._procedure.getProcedures()
      .subscribe(procedures => this.procedures = procedures);
  }

  delete( id: string ) {

    this._procedure.deleteProcedure( id )
            .subscribe ( () =>
            {
              this.snackService.warn('Se ha borrado correctamente el Proceso ');
              this.getProcedures();
            });

  }
  getPositions(){
    this._position.getPositions()
      .subscribe(positions => this.positions = positions);
  }

  getNamePosition(id: string){
    return this.position = this.positions.find(p => p.id === id);

  }

  namePosition(id: string){
    console.log('procedimiento ', this.getNamePosition(id));
    this.name = this.getNamePosition(id).name;
  }

}
