import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { TechnicalEvaluation } from 'src/app/models/technicalEvaluation.model';
import { GLOBAL } from '../../config/global';

@Injectable({
  providedIn: 'root'
})
export class TechnicalEvaluationService {

  private URL = GLOBAL.uri;
  constructor(private http: HttpClient) { }

  getTechnicalEvaluations() {

    let url = this.URL + '/technicalEvaluations';
    return this.http.get( url ).pipe(
              map( (resp: any) => {
                return resp.technicalEvaluations;
              }));

  }

  getTechnicalEvaluation(id: string) {
    let url = this.URL + '/technicalEvaluations/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.technicalEvaluation;
      })
    );
  }

  getTED(id: string) {
    let url = this.URL + '/technicalEvaluations/all/' + `${id}`;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.technicalEvaluation;
      })
    );
  }

  createTechnicalEvaluation(technicalEvaluation: {to: string, via: string,  whose: string, number: string, date: Date}){
    let url = this.URL + '/technicalEvaluations';
    return this.http.post( url, technicalEvaluation);

  }

  updateTechnicalEvaluation(technicalEvaluation: TechnicalEvaluation){
    let url = this.URL + '/technicalEvaluations/'+technicalEvaluation.id;
    return this.http.put( url, technicalEvaluation);
  }

  deleteTechnicalEvaluation( id: string) {

    let url = this.URL + '/technicalEvaluations/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }
}
