import { Component, OnInit, Input, Output, EventEmitter, SimpleChanges } from '@angular/core';
import { TechnicalReport } from 'src/app/models/technicalReport.model';
import { TechnicalReportService } from 'src/app/services/technicalReport/technical-report.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef, MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { DocumentComponent } from '../document/document.component';
import { Document } from 'src/app/models/document.model';
import { DocumentService } from 'src/app/services/document/document.service';
import { SnackService } from 'src/app/services/snack.service';
import { ModalFileUploadComponent } from 'src/app/components/modal-file-upload/modal-file-upload.component';
import { ModalFileUploadService } from 'src/app/components/modal-file-upload/modal-file-upload.service';
import { IStatus } from '../../interfaces/interfaces';
import { StatusService } from '../../services/status/status.service';
import { Status } from '../../models/status.model';
import { User } from '../../models/user';
import * as actions from '../../store/actions/status.actions';
import { AuthService } from '../../services/auth.service';
import { Store } from '@ngrx/store';
import { AppState } from '../../store/app.reducers';
import { FilePreviewComponent } from '../file-preview/file-preview.component';


@Component({
  selector: 'app-technical-report-form',
  templateUrl: './technical-report-form.component.html',
  styleUrls: ['./technical-report-form.component.scss']
})
export class TechnicalReportFormComponent implements OnInit {

  // @Input() status: IStatus;
  // @Output() eventRefresh = new EventEmitter<boolean>();
  codeRequest: string;
  currentStatus: Status;
  currentUser: User;

  // technicalReport: TechnicalReport= new TechnicalReport("","","","","","","","","","");
  public technicalReportForm: FormGroup;
  public technicalReport: TechnicalReport;
  // documentDialogRef: MatDialogRef<DocumentComponent>;
  documents: Document[] = [];
  displayedDocuments: string[] = ['document', 'preview', 'name', 'edit', 'delete'];
  iconFile = '../assets/images/icon/add-file.png';
  constructor(
    public _technicalReport: TechnicalReportService,
    private authService: AuthService,
    private formBuilder: FormBuilder,
    private router: Router,
    private dialog: MatDialog,
    public _document: DocumentService,
    public snackService: SnackService,
    public modalUploadFileService: ModalFileUploadService,
    private store: Store<AppState>
  ) {   }

  ngOnInit(): void {
    // this.activatedRoute.params.subscribe(
    //   params=>{
    //     let id = params['id'];
    //     if( id ){
    // this.activatedRoute.params
    //     .subscribe( ({ id }) => this.getTechnicalReport( id ) );
    //     }
    //   })
    this.currentUser =  JSON.parse(this.authService.getUser()) ;
    this.technicalReportForm = this.formBuilder.group({
          background: [null, Validators.required],
          acquisitionObjetive: [null, Validators.required],
          testInspection: [null, Validators.required],
          supportDocument: [null, Validators.required],
          technicalQuarantee: [null, Validators.required],
          technicalService: [null, Validators.required],
          placeDelivery: [null, Validators.required],
          deliveryTerm: [null, Validators.required],
          price: [null, [Validators.required]],
        });

    this.store.select('status').subscribe(({ status }) => {
          if (status != null) {
            // console.log('el estado actual es ', status);
            this.currentStatus = status;
            if ( status.formId != null) {
              this.getTechnicalReport( status.formId);
            } else {
              this.getTechnicalReport( 'new');
            }
          }
        });


  }

  terminate() {
    console.log('Documentos cargados', this.documents);
    const std: Status = Status.fromState( this.currentStatus );
    if (this.documents.length > 0) {
           std.userId = this.currentUser.id;
           std.status = 'terminado';
           // console.log('current status ', std);
           this.store.dispatch( actions.updateStatus({ status: std }));
         }else {
            std.status = 'pendiente';
         }
  }

  getTechnicalReport(id: string){

    if (id === 'new'){
      return;
    }
    this._technicalReport.getTR(id)
      .subscribe(technicalReport => {
        // if ( !technicalReport ) {
        //   return this.router.navigateByUrl(`/technicalReports`);
        // }
        if (technicalReport) {
          const {background, acquisitionObjetive, testInspection, supportDocument, technicalQuarantee, technicalService, placeDelivery, deliveryTerm, price } = technicalReport;
          this.technicalReport = technicalReport;
          this.documents = technicalReport.documents;
          this.technicalReportForm.setValue({background, acquisitionObjetive, testInspection, supportDocument, technicalQuarantee, technicalService, placeDelivery, deliveryTerm, price });
        }

      });
  }

  save() {

    const {background} = this.technicalReportForm.value;

    if ( this.technicalReport ) {
      // update
      const data = {
        ...this.technicalReportForm.value,
        id: this.technicalReport.id
      };
      this._technicalReport.updateTechnicalReport( data )
        .subscribe( resp => {
          this.snackService.success('Se ha actualizado correctamente la Especificacion Tecnica ' + `${ background }`);
          if (!this.currentStatus.procedureId) {
            this.router.navigateByUrl('/technicalReports');
          } else {
            this.terminate();
          }
        });

    } else {
      // create

      this._technicalReport.createTechnicalReport( this.technicalReportForm.value )
          .subscribe( (resp: any) => {
            // console.log('reporte guardado', resp.technicalReport);
            const std: Status = Status.fromState( this.currentStatus );
            std.formId = resp.technicalReport.id;
            std.userId = this.currentUser.id;
            std.status = 'pendiente';
            // console.log('current status ', std);
            this.store.dispatch( actions.updateStatus({ status: std }));

            this.getTechnicalReport(resp.technicalReport.id);
            this.snackService.success('Se ha creado correctamente la Especificacion Tecnica ' + `${background }`);

            // this.router.navigateByUrl(`/technicalReport/${ resp.technicalReport.id }`)
        });
    }



  }

  delete( id: string ) {

    this._technicalReport.deleteTechnicalReport( id )
            .subscribe ( () =>
            { this._technicalReport.getTechnicalReports();
              this.snackService.warn('Se ha borrado correctamente la Especificacion Tecnica ');
            });

  }

  // document
  getDocuments(){
    this._document.getDocuments()
      .subscribe(documents => this.documents = documents);
  }
  openAddDocumentDialog(){
    const documentDialogRef = this.dialog.open(DocumentComponent, {
      data: { formId: this.technicalReport.id,
              documentId: 'new'
      }
    });
    documentDialogRef.afterClosed().subscribe( result => {
      this.getTechnicalReport(this.technicalReport.id);

    });
  }
  openUpdateDocumentDialog(id: string){
    const documentDialogRef = this.dialog.open(DocumentComponent, {
      data: { documentId: id}
    });
    documentDialogRef.afterClosed().subscribe( result => {
      this.getTechnicalReport(this.technicalReport.id);

      this.terminate();

    });
  }

  openPreviewDocumentDialog(src: string){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.width = '100%';
    dialogConfig.data = {src: src};
    const documentDialogRef = this.dialog.open(FilePreviewComponent, dialogConfig);

  }

  deleteDocument( id: string ) {

    this._document.deleteDocument( id )
            .subscribe ( () =>
            { this._document.getDocuments();
              this.snackService.warn('Se ha borrado correctamente el documento ');
              this.getTechnicalReport(this.technicalReport.id);

              this.terminate();
            });

  }

  createDocument(){
    const date = new Date();
    const document: Document = {id:'',name:"Especificacion Tecnica",type: "", formId:this.technicalReport.id,src:"./uploads",status:"active",createDate: date};
    this._document.createDocument(document)
      .subscribe(() =>
      {
        this._document.getDocuments();
        this.snackService.success('Se ha creado correctamente el documento ');
        this.getTechnicalReport(this.technicalReport.id);
      }
      );
  }

  // sendEvent() {
  //   // console.log('eniva evento');
  //   this.eventRefresh.emit(true);
  // }


  changeFile(id: string) {
    this.dialog.open(ModalFileUploadComponent);
    this.modalUploadFileService.showModal( 'docs', id );

    this.modalUploadFileService.modalNotification
                .subscribe( (resp: any) => {
                  // console.log(resp);

                  this.changeIcon(resp.document.src);
                  this.getTechnicalReport(this.technicalReport.id);
                  this.terminate();
                });
  }

  changeIcon(src) {

    if (src === './uploads') {
      this.iconFile = '../assets/images/icon/add-file.png';
      return;
    }
    const ext = src.split('.');
    // console.log('extencion', ext);

    if (ext[1] === 'pdf') {
      this.iconFile = '../assets/images/icon/pdf.png';
    }

  }

}




