  chargeForms(status: Status) {

    
    // console.log('status ', status.name)
    status.codeRequest != null ? this.infoIn = 'infoReq' : this.infoIn = 'none';
    
    if (status.name === 'Solicitud de adquisicin de bienes' ) {
      this.formsIn = 'request';
    }
    if (status.name === 'Aprobacin Solicitud' ) {
      this.formsIn = 'confirm';
    }
    if (status.name === 'Certificacin presupuestaria' ) {
      this.formsIn = 'certification';
    }
    if (status.name === 'Elaborar especificacin tcnica' ) {
      this.formsIn = 'technical';
    }
    if (status.name === 'Inicio de proceso' ) {
      this.formsIn = 'initial';
    }
    if (status.name === 'Aprobacin inicio de proceso' ) {
      this.formsIn = 'aproved';
    }
    if (status.name === 'Recepcin de propuestas' ) {
      this.formsIn = 'proposal';
    }
    if (status.name === 'Elabora Informe de evaluacin tcnica' ) {
      this.formsIn = 'inform';
    }
    if (status.name === 'Elaborar cuadro comparativo' ) {
      this.formsIn = 'comparative';
    }
    if (status.name === 'Elabora contrato' ) {
      this.formsIn = 'contract';
    }
    if (status.name === 'Firma orden de compra' ) {
      this.formsIn = 'order';
    }
    if (status.name === 'Fin del proceso' ) {
      this.formsIn = 'end';
    }
    // console.log('formIn ', this.formsIn)
    // console.log('valor info', this.infoIn);
  }

