import { Component, OnInit } from '@angular/core';
import { Company } from 'src/app/models/company.model';
import { CompanyService } from 'src/app/services/company/company.service';
import { SnackService } from 'src/app/services/snack.service';

@Component({
  selector: 'app-company',
  templateUrl: './company.component.html',
  styleUrls: ['./company.component.scss']
})
export class CompanyComponent implements OnInit {

  companies: Company[] = [];
  displayedColumns: string[] = ['name', 'direction','nit', 'email', 'phone','edit','delete'];
  constructor(
    public _company: CompanyService,
    public snackService: SnackService,
  ) { }

  ngOnInit(): void {
    this.getCompanies();
  }

  getCompanies(){
    this._company.getCompanies()
    .subscribe(companies => this.companies = companies);
  }

  delete( id: string ) {

    this._company.deleteCompany( id )
            .subscribe ( () =>
            {
              this.snackService.warn('Se ha borrado correctamente a la Empresa');
              this.getCompanies();
            });

  }


}
