import { Component, OnInit } from '@angular/core';
import { ProposalEvaluationService } from 'src/app/services/proposalEvaluation/proposal-evaluation.service';
import { SnackService } from 'src/app/services/snack.service';
import { ProposalEvaluation } from 'src/app/models/proposalEvaluation.model';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { AppState } from 'src/app/store/app.reducers';
import * as actions from '../../store/actions/status.actions';

@Component({
  selector: 'app-proposal-evaluations',
  templateUrl: './proposal-evaluations.component.html',
  styleUrls: ['./proposal-evaluations.component.scss']
})
export class ProposalEvaluationsComponent implements OnInit {

  proposalEvaluations: ProposalEvaluation[] = [];
  displayedColumns: string[] = ['code','edit','delete'];

  constructor(
    public _proposalEvaluation: ProposalEvaluationService,
    public snackService: SnackService,
    private router: Router,
    private store: Store<AppState>

  ) { }

  ngOnInit(): void {
    this.getProposalEvaluations();
  }

  getProposalEvaluations(){
    this._proposalEvaluation.getProposalEvaluations()
      .subscribe(proposalEvaluations => this.proposalEvaluations = proposalEvaluations);
  }

  edit(id: string){
    this.store.dispatch( actions.changeFormId({ formId: id}) );
    this.router.navigateByUrl('/proposalEvaluation/edit');
  }

  delete( id: string ) {

    this._proposalEvaluation.deleteProposalEvaluation( id )
            .subscribe ( () =>
            {
              this.snackService.warn('Se ha borrado correctamente la Evaluacion Propuestas ');
              this.getProposalEvaluations();
            });

  }

}
