import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { AcquisitionRequest } from 'src/app/models/acquisitionRequest.model';
import { GLOBAL } from '../../config/global';

@Injectable({
  providedIn: 'root',
})
export class AcquisitionRequestService {
  private URL = GLOBAL.uri;

  constructor(private http: HttpClient) {}

  getAcquisitionRequests() {
    let url = this.URL + '/acquisitionRequests';
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.acquisitionRequests;
      })
    );
  }

  getAcquisitionRequest(id: string) {
    let url = this.URL + '/acquisitionRequests/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.acquisitionRequest;
      })
    );
  }

  getACQRequest(id: string) {
    let url = this.URL + '/acquisitionRequests/all/' + `${id}`;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.acquisitionRequest;
      })
    );
  }

  createAcquisitionRequest(acquisitionRequest: {code: string, version: string,validity: Date, numRequest: string, requestDate: Date, nPac: string, place: string, question1: string, question2: string, question3: string, coin: string, title: string}){
    let url = this.URL + '/acquisitionRequests';
    return this.http.post( url, acquisitionRequest);

  }

  updateAcquisitionRequest(acquisitionRequest: AcquisitionRequest){
    let url = this.URL + '/acquisitionRequests/'+acquisitionRequest.id;
    return this.http.put( url, acquisitionRequest);
  }

  deleteAcquisitionRequest( id: string) {

    let url = this.URL + '/acquisitionRequests/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }
}
