import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Procedure } from 'src/app/models/procedure.model';
import { ProcedureService } from 'src/app/services/procedure/procedure.service';
import { SnackService } from 'src/app/services/snack.service';
import { ActivatedRoute, Router } from '@angular/router';
import { PositionService } from 'src/app/services/position/position.service';
import { Position } from 'src/app/models/position.model';

@Component({
  selector: 'app-procedure-form',
  templateUrl: './procedure-form.component.html',
  styleUrls: ['./procedure-form.component.scss']
})
export class ProcedureFormComponent implements OnInit {

  public procedureForm:FormGroup;
  public procedure: Procedure;
  public positions: Position [] = [];
  constructor(
    public _procedure: ProcedureService,
    public _position: PositionService,
    public router: Router,
    public activatedRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    public snackService: SnackService,
  ) { }

  ngOnInit(): void {
    this.activatedRoute.params
        .subscribe( ({ id }) => this.getProcedure( id ) );

    this.procedureForm = this.formBuilder.group({
          positionId:[null, Validators.required],
          procedureName:[null, Validators.required],
          order:[null, Validators.required],

        });
    this.getPositions();
  }

  getProcedure(id: string){

    if(id === 'new'){
      return;
    }
    this._procedure.getProcedure(id)
      .subscribe(procedure => {
        if ( !procedure ) {
          return this.router.navigateByUrl(`/procedures`);
        }
        const {positionId, procedureName, order} = procedure;
        this.procedure = procedure;
        this.procedureForm.setValue({positionId, procedureName, order });
      })
  }

  save() {

    const {procedureName} = this.procedureForm.value;

    if ( this.procedure ) {
      // update
      const data = {
        ...this.procedureForm.value,
        id: this.procedure.id
      }
      this._procedure.updateProcedure( data )
        .subscribe( resp => {
          this.snackService.success('Se ha actualizado correctamente el Proceso ' + `${ procedureName }`);
        })

    } else {
      // create

      this._procedure.createProcedure( this.procedureForm.value )
          .subscribe( (resp: any) => {
            this.snackService.success('Se ha creado correctamente el Proceso '+ `${procedureName }`);
            this.router.navigateByUrl(`/procedure/${ resp.procedure.id }`)
        })
    }



  }

  getPositions(){
    this._position.getPositions().subscribe(positions => this.positions = positions);
  }

}
