import { Component, OnInit, Inject } from '@angular/core';
import { Document } from 'src/app/models/document.model';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DocumentService } from 'src/app/services/document/document.service';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SnackService } from 'src/app/services/snack.service';

@Component({
  selector: 'app-document',
  templateUrl: './document.component.html',
  styleUrls: ['./document.component.scss']
})
export class DocumentComponent implements OnInit {

  document: Document;
  public documentForm: FormGroup;
  formId: string;
  documentId: string;

  constructor(
    public _document: DocumentService,
    private dialogRef: MatDialogRef<DocumentComponent>,
    private documentFormBuilder: FormBuilder,
    @Inject(MAT_DIALOG_DATA) public data: DocumentComponent,
    public snackService: SnackService,

  ) { }

  ngOnInit(): void {
    if(this.data.documentId !== 'new'){
      this.getDocument(this.data.documentId);
    }

    this.documentForm = this.documentFormBuilder.group({
      name:[null, Validators.required],
      type:[null],
      formId:[this.data.formId],
      src:[null],


    });
  }
  getDocument(id: string){
    this._document.getDocument(id)
      .subscribe(document => {
        if(!document){
          this.onCloseDialog();
        }
        const {name, type, formId, src} = document;
        this.document = document;
        this.documentForm.setValue({name, type, formId, src});
      })
  }
  onCloseDialog(){

    this.dialogRef.close();
  }
  save() {

    const {name} = this.documentForm.value;
    if ( this.document ) {
      // update
      const data = {
        ...this.documentForm.value,
        id: this.document.id,
        formId: this.document.formId
      }
      console.log(data);
      this._document.updateDocument( data )
        .subscribe( resp => {
          this.snackService.success('Se ha actualizado correctamente el documento ' + `${ name }`);
          this.onCloseDialog();
        })

    } else {
      // create

      this._document.createDocument( this.documentForm.value )
          .subscribe( (resp: any) => {
            this.snackService.success('Se ha creado correctamente el documento '+ `${name }`);
            this.onCloseDialog();
        })
    }



  }

  delete( id: string ) {

    this._document.deleteDocument( id )
            .subscribe ( () =>
            { this._document.getDocuments();
              this.snackService.success('Se ha creado borrado el documento ');
            });

  }


}
