import { Component, OnInit, Input, SimpleChanges, ViewChild, ElementRef, AfterViewChecked } from '@angular/core';
import { IStatus } from 'src/app/interfaces/interfaces';
import { StatusService } from '../../../services/status/status.service';
import { ProcedureService } from '../../../services/procedure/procedure.service';
import { Procedure } from '../../../models/procedure.model';
import { Status } from '../../../models/status.model';
import { Store } from '@ngrx/store';
import { AppState } from '../../../store/app.reducers';

@Component({
  selector: 'app-tracing',
  templateUrl: './tracing.component.html',
  styleUrls: ['./tracing.component.scss']
})
export class TracingComponent implements OnInit, AfterViewChecked {
  
  currentStatus: Status;
  tracer: IStatus [] = [];
  procedure: Procedure = new Procedure(null, null, '', 0);
  @ViewChild('container') private myScrollContainer: ElementRef;

  constructor(
              private statusService: StatusService,
              private procedureService: ProcedureService,
              private store: Store<AppState>
              ) { }

  ngOnInit(): void {

    this.store.select('status').subscribe(({ status }) => {
      if (status != null) {
        // console.log('el estado actual es ', status);
        this.currentStatus = status;
        if ( status == null) { return; }
        if ( status.codeRequest ) {
          this.getStatus(this.currentStatus.codeRequest);
          this.getNextProcedure(this.currentStatus.procedureId);
        } else {
          this.tracer = null;
          this.procedure = null;
        }

      }
    });
    // this.scrollToBottom();
    // console.log('entro al init');
  }

  ngAfterViewChecked () {
    // lo llama varias veces
    this.scrollToBottom();
    // console.log('entro al cheked');
  }

  getStatus(codeRequest: string) {
    this.statusService.getStatusByRequest(codeRequest).subscribe( tracer => {
      // console.log('traza ', tracer);
      this.tracer = tracer;
    } );
  }

  getNextProcedure(procedureId: string) {
    this.procedureService.getProcedure(procedureId).subscribe( currentProcedure => {
      const newOrder = currentProcedure.order + 1;
      this.procedureService.getProcedureByOrder(newOrder).subscribe( nextProcedure => {
        this.procedure = nextProcedure;
        // console.log('siguiente procedimiento', this.procedure);
      });
    });
  }

  scrollToBottom(): void {

    try {
        // console.log('estro al scroll', this.myScrollContainer.nativeElement.scrollWidth);


        this.myScrollContainer.nativeElement.scrollLeft = this.myScrollContainer.nativeElement.scrollWidth;
    
      } catch (err) { console.log('error en le scroll ', err); }
}

}
