import { Component, OnInit } from '@angular/core';
import { Office } from 'src/app/models/office.model';
import { OfficeService } from 'src/app/services/office/office.service';
import { MatDialogRef } from '@angular/material/dialog';
import { SnackService } from 'src/app/services/snack.service';

@Component({
  selector: 'app-office',
  templateUrl: './office.component.html',
  styleUrls: ['./office.component.scss']
})
export class OfficeComponent implements OnInit {

  offices: Office[] = [];
  displayedColumns: string[] = ['name', 'companyId', 'supOfficeId','edit', 'delete'];

  constructor(
    public _office: OfficeService,
    public snackService: SnackService,
    //private dialogRef: MatDialogRef<OfficeComponent>,
  ) { }

  ngOnInit(): void {
    this.getOffices()
  }

  getOffices(){
    this._office.getOffices()
    .subscribe(offices => this.offices = offices);
}

delete( id: string ) {

  this._office.deleteOffice( id )
          .subscribe ( () =>
          { this._office.getOffices();
            this.snackService.warn('Se ha borrado correctamente la Unidad ');
          });

}





}
