import { Component, OnInit } from '@angular/core';
import { SnackService } from 'src/app/services/snack.service';
import { ProcessStart } from 'src/app/models/processStart.model';
import { ProcessStartService } from 'src/app/services/processStart/process-start.service';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { AppState } from 'src/app/store/app.reducers';
import * as actions from '../../store/actions/status.actions';


@Component({
  selector: 'app-process-start',
  templateUrl: './process-start.component.html',
  styleUrls: ['./process-start.component.scss']
})
export class ProcessStartComponent implements OnInit {

  processStarts: ProcessStart[] = [];
  displayedColumns: string[] = ['category','startDate', 'endDate','edit','delete'];

  constructor(
    public _processStart: ProcessStartService,
    public snackService: SnackService,
    private router: Router,
    private store: Store<AppState>
  ) { }

  ngOnInit(): void {
    this.getProcessStarts();
  }

  getProcessStarts(){
    this._processStart.getProcessStarts()
      .subscribe(processStarts => this.processStarts = processStarts);
  }

  edit(id: string){
    this.store.dispatch( actions.changeFormId({ formId: id}) );
    this.router.navigateByUrl('/processStart/edit');
  }

  delete(id: string){
    this._processStart.deleteProcessStart(id)
      .subscribe(() =>
      {
        this.snackService.warn('Se ha borrado correctamente la Orden de Compra ');
        this.getProcessStarts();
      })
  }
}
