import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { TechnicalReport } from 'src/app/models/technicalReport.model';
import { GLOBAL } from '../../config/global';

@Injectable({
  providedIn: 'root'
})
export class TechnicalReportService {

  private URL = GLOBAL.uri;
  constructor(private http: HttpClient) { }

  getTechnicalReports() {

    let url = this.URL + '/technicalReports';
    return this.http.get( url ).pipe(
              map( (resp: any) => {
                return resp.technicalReports;
              }));

  }

  getTechnicalReport(id: string) {
    let url = this.URL + '/technicalReports/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.technicalReport;
      })
    );
  }

  getTR(id: string) {
    let url = this.URL + '/technicalReports/all/' + `${id}`;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.technicalReport;
      })
    );
  }

  createTechnicalReport(technicalReport: {background: string, acquisitionObjetive: string, testInspection: string, supportDocument: string, technicalQuarantee: string, technicalService: string, placeDelivery: string, deliveryTerm: string, price: string}){
    let url = this.URL + '/technicalReports';
    return this.http.post( url, technicalReport);

  }

  updateTechnicalReport(technicalReport: TechnicalReport){
    let url = this.URL + '/technicalReports/'+technicalReport.id;
    return this.http.put( url, technicalReport);
  }

  deleteTechnicalReport( id: string) {

    let url = this.URL + '/technicalReports/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }
}
