import { Component, OnInit, Inject } from '@angular/core';
import { Product } from 'src/app/models/product.model';
import { ProductService } from 'src/app/services/product/product.service';
import { Router, ActivatedRoute } from '@angular/router';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { SnackService } from 'src/app/services/snack.service';

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.scss']
})
export class ProductComponent implements OnInit {

  product: Product;// = new Product("","","","",null,"");
  public productForm: FormGroup;
  requestId: string;
  productId: string;
  constructor(
    public _product: ProductService,
    public router: Router,
    public activatedRoute: ActivatedRoute,
    private dialogRef: MatDialogRef<ProductComponent>,
    private productFormBuilder: FormBuilder,
    @Inject(MAT_DIALOG_DATA) public data: ProductComponent,
    public snackService: SnackService,

  ) {  }

  ngOnInit(): void {
    if(this.data.productId !== 'new'){
      this.getProduct(this.data.productId);
    }

    this.productForm = this.productFormBuilder.group({
      name:[null, Validators.required],
      description:[null, Validators.required],
      quantity:[null, Validators.required],
      price:[null, [Validators.required, Validators.pattern(/^(0|[1-9]\d*)(\.\d+)?$/)]],
      requestId:[this.data.requestId],

    });
  }
  getProduct(id: string){
    this._product.getProduct(id)
      .subscribe(product => {
        if(!product){
          this.onCloseDialog();
        }
        const {name, description, quantity, price, requestId} = product;
        this.product = product;
        this.productForm.setValue({name, description, quantity, price, requestId });
      })
  }
  onCloseDialog(){

    this.dialogRef.close();
  }
  save() {

    const {name} = this.productForm.value;
    if ( this.product ) {
      // update
      const data = {
        ...this.productForm.value,
        id: this.product.id,
        requestId: this.product.requestId
      }
      console.log(data);
      this._product.updateProduct( data )
        .subscribe( resp => {
          this.snackService.success('Se ha actualizado correctamente el Item ' + `${ name }`);
          this.onCloseDialog();
        })

    } else {
      // create

      this._product.createProduct( this.productForm.value )
          .subscribe( (resp: any) => {
            this.snackService.success('Se ha creado correctamente el Item '+ `${name }`);
            this.onCloseDialog();
        })
    }



  }

  delete( id: string ) {

    this._product.deleteProduct( id )
            .subscribe ( () =>
            { this._product.getProducts();
              this.snackService.warn('Se ha eliminado correctamente el Item ');
            });

  }



}
