import { Component, OnInit, ViewChild } from '@angular/core';
import { StatusService } from '../../services/status/status.service';
import { HttpErrorResponse } from '@angular/common/http';
import { MatTableDataSource } from '@angular/material/table';
import { Status } from '../../models/status.model';
import { MatDialog, MatDialogConfig, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { IStatus } from '../../interfaces/interfaces';
import { DetailComponent } from './detail/detail.component';

@Component({
  selector: 'app-performed',
  templateUrl: './performed.component.html',
  styleUrls: ['./performed.component.scss']
})
export class PerformedComponent implements OnInit {

  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['name', 'dateAt', 'status', 'actions'];
  data: Status [] = [];
  statuss: Status[] = [];
  loading = false;
  error: any;
  detailComp: DetailComponent;

  page = 0;
  size = 5;

  // @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  
  constructor( public dialog: MatDialog, public statusService: StatusService ) { }

  ngOnInit(): void {
    this.submit();
  }

  submit() {
    this.statusService.getStatusByUserId(this.statusService.searchform.value)
          .subscribe(
              (data) => {
                // console.log('Se envio el form', data);
                this.statuss = data.status;
                this.data = this.statuss;
                this.getData({pageIndex: this.page, pageSize: this.size});
              },
              (error: HttpErrorResponse) => {
                console.log(error);
              }
          )
  }


// opciones para el paginador
  getData(obj) {
    let index = 0,
    startingIndex = obj.pageIndex * obj.pageSize,
    endingIndex = startingIndex + obj.pageSize;

    this.data = this.statuss.filter(() => {
      index++;
      return (index > startingIndex && index <= endingIndex) ? true : false;
    });
  }

  detail( row) {
    this.statusService.reciveStatus(row);
    // this.userService.populateForm(row);
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '60%';
    const dialogRef = this.dialog.open(DetailComponent, dialogConfig);

    dialogRef.afterClosed().subscribe(result => {
      // this.getUsers();
    });
  }
}
