import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ProposalComparison } from 'src/app/models/proposalComparison.model';
import { ProposalComparisonService } from 'src/app/services/proposalComparison/proposal-comparison.service';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { DocumentService } from 'src/app/services/document/document.service';
import { SnackService } from 'src/app/services/snack.service';
import { ModalFileUploadService } from 'src/app/components/modal-file-upload/modal-file-upload.service';
import { DocumentComponent } from '../document/document.component';
import { Document } from 'src/app/models/document.model';
import { FilePreviewComponent } from '../file-preview/file-preview.component';
import { ModalFileUploadComponent } from 'src/app/components/modal-file-upload/modal-file-upload.component';
import { Status } from '../../models/status.model';
import * as actions from '../../store/actions/status.actions';
import { User } from '../../models/user';
import { AuthService } from '../../services/auth.service';
import { Store } from '@ngrx/store';
import { AppState } from '../../store/app.reducers';
import { Router } from '@angular/router';

@Component({
  selector: 'app-proposal-comparison-form',
  templateUrl: './proposal-comparison-form.component.html',
  styleUrls: ['./proposal-comparison-form.component.scss']
})
export class ProposalComparisonFormComponent implements OnInit {
  codeRequest: string;
  currentStatus: Status;
  currentUser: User;
  public proposalComparisonForm: FormGroup;
  public proposalComparison: ProposalComparison;
  documents: Document[] = [];
  displayedDocuments: string[] = ['document', 'preview', 'name', 'edit', 'delete'];
  iconFile = '../assets/images/icon/add-file.png';

  constructor(
    public _proposalComparison: ProposalComparisonService,
    private authService: AuthService,
    private formBuilder: FormBuilder,
    private router: Router,
    private dialog: MatDialog,
    public _document: DocumentService,
    public snackService: SnackService,
    public modalUploadFileService: ModalFileUploadService,
    private store: Store<AppState>
  ) { }

  ngOnInit(): void {
    this.currentUser =  JSON.parse(this.authService.getUser()) ;
    this.proposalComparisonForm = this.formBuilder.group({
          code: [null, Validators.required],
          description: [null],
          provider: [null, Validators.required],

        });
    this.store.select('status').subscribe(({ status }) => {
          if (status != null) {
            // console.log('el estado actual es ', status);
            this.currentStatus = status;
            if ( status.formId != null) {
              this.getProposalComparison( status.formId);
            } else {
              this.getProposalComparison( 'new');
            }
          }
        });
  }

  getProposalComparison(id: string){

    if (id === 'new'){
      return;
    }
    this._proposalComparison.getPCD(id)
      .subscribe(proposalComparison => {

        if ( proposalComparison ) {
          const {code, description,provider} = proposalComparison;
          this.proposalComparison = proposalComparison;
          this.documents = proposalComparison.documents;
          this.proposalComparisonForm.setValue({code, description,provider});
        }
      });
  }

  terminate() {
    if (this.currentStatus.procedureId) {
      const std: Status = Status.fromState( this.currentStatus );
      if (this.documents.length > 0) {
            std.userId = this.currentUser.id;
            std.status = 'terminado';
            // console.log('current status ', std);
            this.store.dispatch( actions.updateStatus({ status: std }));
          }else {
              std.status = 'pendiente';
          }
    }
  }

  save() {

    const {code} = this.proposalComparisonForm.value;

    if ( this.proposalComparison ) {
      // update
      const data = {
        ...this.proposalComparisonForm.value,
        id: this.proposalComparison.id
      };
      this._proposalComparison.updateProposalComparison( data )
        .subscribe( resp => {
          this.snackService.success('Se ha actualizado correctamente la Comparacion Propuesta ' + `${ code }`);
          
          if (!this.currentStatus.procedureId) {
            this.router.navigateByUrl('/proposalComparisons');
          } else {
            this.terminate();
          }
        });

    } else {
      // create

      this._proposalComparison.createProposalComparison( this.proposalComparisonForm.value )
          .subscribe( (resp: any) => {
            // console.log('valor de la certificacin guardada', resp);
            if (this.currentStatus.procedureId) {
              const std: Status = Status.fromState( this.currentStatus );
              std.formId = resp.proposalComparison.id;
              std.userId = this.currentUser.id;
              std.status = 'pendiente';
              // console.log('current status ', std);
              this.store.dispatch( actions.updateStatus({ status: std }));
            }

            this.getProposalComparison(resp.proposalComparison.id);
            this.snackService.success('Se ha creado correctamente la Comparacion Propuesta ' + `${code }`);

            // this.router.navigateByUrl(`/proposalComparison/${ resp.proposalComparison.id }`)
        });
    }



  }

  delete( id: string ) {

    this._proposalComparison.deleteProposalComparison( id )
            .subscribe ( () =>
            { this._proposalComparison.getProposalComparisons();
              this.snackService.warn('Se ha borrado correctamente la Comparacion Propuesta ');
            });

  }

  // document
  getDocuments(){
    this._document.getDocuments()
      .subscribe(documents => this.documents = documents);
  }
  openAddDocumentDialog(){
    const documentDialogRef = this.dialog.open(DocumentComponent, {
      data: { formId: this.proposalComparison.id,
              documentId: 'new'
      }
    });
    documentDialogRef.afterClosed().subscribe( result => {

      this.getProposalComparison(this.proposalComparison.id);
      // this.sendEvent();
    });
  }
  openUpdateDocumentDialog(id: string){
    const documentDialogRef = this.dialog.open(DocumentComponent, {
      data: { documentId: id}
    });
    documentDialogRef.afterClosed().subscribe( result => {
      this.getProposalComparison(this.proposalComparison.id);
      this.terminate();
    });
  }

  openPreviewDocumentDialog(src: string){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.width = '100%';
    dialogConfig.data = {src};
    const documentDialogRef = this.dialog.open(FilePreviewComponent, dialogConfig);

  }

  deleteDocument( id: string ) {

    this._document.deleteDocument( id )
            .subscribe ( () =>
            { this._document.getDocuments();
              this.snackService.warn('Se ha borrado correctamente el documento ');
              this.getProposalComparison(this.proposalComparison.id);
              this.terminate();
            });

  }

  /* sendEvent() {
    // console.log('eniva evento');
    this.eventRefresh.emit(true);
  } */

  createDocument(){
    const document: Document = {id:'',name:"Comparacion Propuesta",type: "", formId:this.proposalComparison.id,src:"./uploads", status:"active", createDate: new Date()};
    this._document.createDocument(document)
      .subscribe(() =>
      {
        this._document.getDocuments();
        this.snackService.success('Se ha creado correctamente el documento ');
        this.getProposalComparison(this.proposalComparison.id);
      }
      );
  }

  changeFile(id: string) {
    this.dialog.open(ModalFileUploadComponent);
    this.modalUploadFileService.showModal( 'docs', id );

    this.modalUploadFileService.modalNotification
                .subscribe( (resp: any) => {
                  // console.log(resp);

                  this.changeIcon(resp.document.src);
                  this.getProposalComparison(this.proposalComparison.id);
                  // this.sendEvent();
                  this.terminate();
                });
  }

  changeIcon(src) {

    if (src === './uploads') {
      this.iconFile = '../assets/images/icon/add-file.png';
      return;
    }
    const ext = src.split('.');
    // console.log('extencion', ext);

    if (ext[1] === 'pdf') {
      this.iconFile = '../assets/images/icon/pdf.png';
    }

  }


}
