import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { Office } from 'src/app/models/office.model';
import { GLOBAL } from '../../config/global';
@Injectable({
  providedIn: 'root'
})
export class OfficeService {
  private URL = GLOBAL.uri;
  constructor(private http: HttpClient) { }

  getOffices() {
    let url = this.URL + '/offices';
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.offices;
      })
    );
  }

  getOffice(id: string) {
    let url = this.URL + '/offices/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.office;
      })
    );
  }

  getOF(id: string) {
    let url = this.URL + '/offices/all/' + `${id}`;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.office;
      })
    );
  }

  createOffice(office: {name: string, companyId: string,supOfficeId: string}){
    let url = this.URL + '/offices';
    return this.http.post( url, office);

  }

  updateOffice(office: Office){
    let url = this.URL + '/offices/'+office.id;
    return this.http.put( url, office);
  }

  deleteOffice( id: string) {

    let url = this.URL + '/offices/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }

}
