import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { BudgetCertification } from 'src/app/models/budgetCertification.model';
import { GLOBAL } from '../../config/global';
@Injectable({
  providedIn: 'root'
})
export class BudgetCertificationService {
  private URL = GLOBAL.uri;
  constructor(private http: HttpClient) { }

  getBudgetCertifications() {
    let url = this.URL + '/budgetCertifications';
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.budgetCertifications;
      })
    );
  }

  getBudgetCertification(id: string) {
    let url = this.URL + '/budgetCertifications/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.budgetCertification;
      })
    );
  }

  getBC(id: string) {
    let url = this.URL + '/budgetCertifications/all/' + `${id}`;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.budgetCertification;
      })
    );
  }

  createBudgetCertification(budgetCertification: {reserveCode: string}){
    let url = this.URL + '/budgetCertifications';
    return this.http.post( url, budgetCertification);

  }

  updateBudgetCertification(budgetCertification: BudgetCertification){
    let url = this.URL + '/budgetCertifications/'+ budgetCertification.id;
    return this.http.put( url, budgetCertification);
  }

  deleteBudgetCertification( id: string) {

    let url = this.URL + '/budgetCertifications/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }

}
