import { Component, OnInit, Inject } from '@angular/core';
import { Office } from 'src/app/models/office.model';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { OfficeService } from 'src/app/services/office/office.service';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { PositionComponent } from '../position/position.component';
import { Router, ActivatedRoute } from '@angular/router';
import { PositionFormComponent } from '../position/position-form.component';
import { PositionService } from 'src/app/services/position/position.service';
import { SnackService } from 'src/app/services/snack.service';

@Component({
  selector: 'app-office-form',
  templateUrl: './office-form.component.html',
  styleUrls: ['./office-form.component.scss']
})
export class OfficeFormComponent implements OnInit {

  office: Office;
  public officeForm: FormGroup;
  companyId: string;
  positionDialogRef: MatDialogRef<PositionComponent>;
  //positionFormDialogRef: MatDialogRef<PositionFormComponent>;
  positions: Position[] = [];
  displayedColumns: string[] = ['name', 'active','edit', 'delete'];
  constructor(
    public _office: OfficeService,
    private officeFormBuilder: FormBuilder,
    private dialog: MatDialog,
    public router: Router,
    public activatedRoute: ActivatedRoute,
    public _position: PositionService,
    public snackService: SnackService,
  ) { }

  ngOnInit(): void {

    this.activatedRoute.params
    .subscribe( ({ id }) => this.getOffice( id ) );
    this.officeForm = this.officeFormBuilder.group({
      name:[null, Validators.required],
      companyId:[null],
      supOfficeId:[null],


    });

  }

  getOffice(id: string){
    if(id==='new'){
      return;
    }
    this._office.getOF(id)
      .subscribe(office => {
        if(!office){
          return this.router.navigateByUrl(`/offices`);
        }
        const {name, companyId, supOfficeId} = office;
        this.office = office;
        this.positions = office.positions;
        this.officeForm.setValue({name, companyId, supOfficeId });
      })
  }

  save() {

    const {name} = this.officeForm.value;
    if ( this.office ) {
      // update
      const data = {
        ...this.officeForm.value,
        id: this.office.id,
        companyId: this.office.companyId
      }
      console.log(data);
      this._office.updateOffice( data )
        .subscribe( resp => {
          this.snackService.success('Se ha actualizado correctamente la Unidad ' + `${ name }`);
        })

    } else {
      // create

      this._office.createOffice( this.officeForm.value )
          .subscribe( (resp: any) => {
            this.snackService.success('Se ha creado correctamente la Unidad ' + `${ name }`);
            this.router.navigateByUrl(`/office/${ resp.office.id }`)
        })
    }



  }

  openViewPositionDialog(){
    this.positionDialogRef = this.dialog.open(PositionComponent);
  }

  openAddPositionDialog(){
    const positionFormDialogRef = this.dialog.open(PositionFormComponent, {
      data: { officeId: this.office.id,
              positionId: 'new'
      }
    });
    positionFormDialogRef.afterClosed().subscribe( result => {
      this.getOffice(this.office.id);
    });
  }

  openUpdatePositionDialog(id: string){
    const positionFormDialogRef = this.dialog.open(PositionFormComponent, {
      data: { positionId: id}
    });
    positionFormDialogRef.afterClosed().subscribe( result => {
      this.getOffice(this.office.id);
    });

  }

  getPositions(){
    this._position.getPositions()
    .subscribe(positions => this.positions = positions);
}

deletePosition( id: string ) {

  this._position.deletePosition( id )
          .subscribe ( () =>
          { this.getOffice(this.office.id);
            this.snackService.warn('Se ha borrado correctamente el cargo ');
          });

}

}
