import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { LegalContract } from 'src/app/models/legalContract.model';
import { LegalContractService } from 'src/app/services/legalContract/legal-contract.service';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { DocumentService } from 'src/app/services/document/document.service';
import { SnackService } from 'src/app/services/snack.service';
import { ModalFileUploadService } from 'src/app/components/modal-file-upload/modal-file-upload.service';
import { DocumentComponent } from '../document/document.component';
import { Document } from 'src/app/models/document.model';
import { FilePreviewComponent } from '../file-preview/file-preview.component';
import { ModalFileUploadComponent } from 'src/app/components/modal-file-upload/modal-file-upload.component';
import { Status } from '../../models/status.model';
import { User } from '../../models/user';
import * as actions from '../../store/actions/status.actions';
import { AuthService } from '../../services/auth.service';
import { Store } from '@ngrx/store';
import { AppState } from '../../store/app.reducers';
import { Router } from '@angular/router';

@Component({
  selector: 'app-legal-contract-form',
  templateUrl: './legal-contract-form.component.html',
  styleUrls: ['./legal-contract-form.component.scss']
})
export class LegalContractFormComponent implements OnInit {

  codeRequest: string;
  currentStatus: Status;
  currentUser: User;

  public legalContractForm: FormGroup;
  public legalContract: LegalContract;
  documents: Document[] = [];
  displayedDocuments: string[] = ['document','preview','name', 'edit', 'delete'];
  iconFile = '../assets/images/icon/add-file.png';

  constructor(
    public _legalContract: LegalContractService,
    private authService: AuthService,
    private formBuilder: FormBuilder,
    private router: Router,
    private dialog: MatDialog,
    public _document: DocumentService,
    public snackService: SnackService,
    public modalUploadFileService: ModalFileUploadService,
    private store: Store<AppState>
  ) { }

  ngOnInit(): void {

    this.currentUser =  JSON.parse(this.authService.getUser()) ;

    this.legalContractForm = this.formBuilder.group({
          code:[null, Validators.required],
          description:[null, Validators.required],
          responsable:[null, Validators.required],

        });
    this.store.select('status').subscribe(({ status }) => {
      if (status != null) {
        // console.log('el estado actual es ', status);
        this.currentStatus = status;
        if ( status.formId != null) {
          this.getLegalContract( status.formId);
        } else {
          this.getLegalContract('new');
        }
      }
    });
  }

  getLegalContract(id: string){

    if(id === 'new'){
      return;
    }
    this._legalContract.getPCD(id)
      .subscribe(legalContract => {
        if ( legalContract ) {
          const {code, description,responsable} = legalContract;
          this.legalContract = legalContract;
          this.documents = legalContract.documents;
          this.legalContractForm.setValue({code, description,responsable});
        }
      })
  }

  terminate() {
    if ( this.currentStatus.procedureId ) {
      const std: Status = Status.fromState( this.currentStatus );
      if (this.documents.length > 0) {
            std.userId = this.currentUser.id;
            std.status = 'terminado';
            // console.log('current status ', std);
            this.store.dispatch( actions.updateStatus({ status: std }));
          }else {
              std.status = 'pendiente';
          }
    }
  }

  save() {

    const {code} = this.legalContractForm.value;

    if ( this.legalContract ) {
      // update
      const data = {
        ...this.legalContractForm.value,
        id: this.legalContract.id
      }
      this._legalContract.updateLegalContract( data )
        .subscribe( resp => {
          this.snackService.success('Se ha actualizado correctamente el Contrato Legal ' + `${ code }`);
          if (!this.currentStatus.procedureId) {
            this.router.navigateByUrl('/legalContracts');
          } else {
            this.terminate();
          }
        })

    } else {
      // create

      this._legalContract.createLegalContract( this.legalContractForm.value )
          .subscribe( (resp: any) => {
            // console.log('valor de la certificacin guardada', resp);
            if ( this.currentStatus.procedureId ) {
              const std: Status = Status.fromState( this.currentStatus );
              std.formId = resp.legalContract.id;
              std.userId = this.currentUser.id;
              std.status = 'pendiente';
              // console.log('current status ', std);
              this.store.dispatch( actions.updateStatus({ status: std }));
            }
            this.getLegalContract(resp.legalContract.id);
            this.snackService.success('Se ha creado correctamente el Contrato Legal '+ `${code }`);

            // this.router.navigateByUrl(`/legalContract/${ resp.legalContract.id }`)
        })
    }



  }

  delete( id: string ) {

    this._legalContract.deleteLegalContract( id )
            .subscribe ( () =>
            { this._legalContract.getLegalContracts();
              this.snackService.warn('Se ha borrado correctamente el Contrato Legal ');
            });

  }

  //document
  getDocuments(){
    this._document.getDocuments()
      .subscribe(documents => this.documents = documents);
  }
  openAddDocumentDialog(){
    const documentDialogRef = this.dialog.open(DocumentComponent, {
      data: { formId: this.legalContract.id,
              documentId: 'new'
      }
    });
    documentDialogRef.afterClosed().subscribe( result => {

      this.getLegalContract(this.legalContract.id);
      //this.sendEvent();

    });
  }
  openUpdateDocumentDialog(id: string){
    const documentDialogRef = this.dialog.open(DocumentComponent, {
      data: { documentId: id}
    });
    documentDialogRef.afterClosed().subscribe( result => {
      this.getLegalContract(this.legalContract.id);
      this.terminate();
    });
  }

  openPreviewDocumentDialog(src: string){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.width = '100%';
    dialogConfig.data = {src: src};
    const documentDialogRef = this.dialog.open(FilePreviewComponent, dialogConfig);

  }

  deleteDocument( id: string ) {

    this._document.deleteDocument( id )
            .subscribe ( () =>
            { this._document.getDocuments();
              this.snackService.warn('Se ha borrado correctamente el documento ');
              this.getLegalContract(this.legalContract.id);
              this.terminate();
            });

  }

  /* sendEvent() {
    // console.log('eniva evento');
    this.eventRefresh.emit(true);
  } */

  createDocument(){

    const document: Document = {id:'',name:"Contrato Legal",type: "", formId:this.legalContract.id,src:"./uploads", status:"active", createDate: new Date()};
    this._document.createDocument(document)
      .subscribe(() =>
      {
        this._document.getDocuments();
        this.snackService.success('Se ha creado correctamente el documento ');
        this.getLegalContract(this.legalContract.id);
      }
      )
  }

  changeFile(id: string) {
    this.dialog.open(ModalFileUploadComponent);
    this.modalUploadFileService.showModal( 'docs', id );

    this.modalUploadFileService.modalNotification
                .subscribe( (resp: any) => {
                  // console.log(resp);

                  this.changeIcon(resp.document.src);
                  this.getLegalContract(this.legalContract.id);
                  //this.sendEvent();
                  this.terminate();
                });
  }

  changeIcon(src) {

    if (src == "./uploads") {
      this.iconFile = '../assets/images/icon/add-file.png';
      return;
    }
    const ext = src.split('.');
    // console.log('extencion', ext);

    if (ext[1] === 'pdf') {
      this.iconFile = '../assets/images/icon/pdf.png';
    }

  }


}
