import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { ProposalProvider } from 'src/app/models/proposalProvider.model';
import { GLOBAL } from '../../config/global';

@Injectable({
  providedIn: 'root'
})
export class ProposalProviderService {
  private URL = GLOBAL.uri;
  constructor(private http: HttpClient) { }

  getProposalProviders() {
    let url = this.URL + '/proposalProviders';
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.proposalProviders;
      })
    );
  }

  getProposalProvider(id: string) {
    let url = this.URL + '/proposalProviders/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.proposalProvider;
      })
    );
  }

  getPPForm(id: string) {
    let url = this.URL + '/proposalProviders/form/' + `${id}`;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.proposalProviders;
      })
    );
  }


  createProposalProvider(proposalProvider: {name: string, nit: string, date: Date, formId: string}){
    let url = this.URL + '/proposalProviders';
    return this.http.post( url, proposalProvider);

  }

  updateProposalProvider(proposalProvider: ProposalProvider){
    let url = this.URL + '/proposalProviders/'+ proposalProvider.id;
    return this.http.put( url, proposalProvider);
  }

  deleteProposalProvider( id: string) {

    let url = this.URL + '/proposalProviders/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }

}
