import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { Position } from 'src/app/models/position.model';
import { GLOBAL } from '../../config/global';

@Injectable({
  providedIn: 'root'
})
export class PositionService {

  private URL = GLOBAL.uri;
  constructor(private http: HttpClient) { }

  getPositions() {
    let url = this.URL + '/positions';
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.positions;
      })
    );
  }

  getPosition(id: string) {
    let url = this.URL + '/positions/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.position;
      })
    );
  }

  createPosition(position: {name: string, officeId: string,active: boolean}){
    let url = this.URL + '/positions';
    return this.http.post( url, position);

  }

  updatePosition(position: Position){
    let url = this.URL + '/positions/'+position.id;
    return this.http.put( url, position);
  }

  deletePosition( id: string) {

    let url = this.URL + '/positions/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }
}
