import { Timestamp } from 'rxjs/internal/operators/timestamp';

export class Status{

  static fromState({id, procedureId, userId, formId, status, dateAt, current, codeRequest, name }): Status {
      return new Status(id, procedureId, userId, formId, status, dateAt, current, codeRequest, name);
  }

  constructor(
    public id: string,
    public procedureId: string,
    public userId: string,
    public formId: string,
    public status: string,
    public dateAt: Date,
    public current: boolean,
    public codeRequest: string,
    public name: string,
  ){}
}
