import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { Procedure } from 'src/app/models/procedure.model';
import { IProcedure } from '../../interfaces/interfaces';
import { GLOBAL } from '../../config/global';
@Injectable({
  providedIn: 'root'
})
export class ProcedureService {
  private URL = GLOBAL.uri;
  constructor(private http: HttpClient) {}

  getProcedures() {
    let url = this.URL + '/procedures';
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.procedures;
      })
    );
  }

  getProcedure(id: string) {
    let url = this.URL + '/procedures/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.procedure;
      })
    );
  }

  getProcedureByOrder(order: number) {
    let url = this.URL + '/procedures/byorder/' + order;
    return this.http.get<IProcedure>(url).pipe(
      map((resp: any) => {
       
        return resp.procedure;
      })
    );
  }

  getProcedureByPosition(positionId: string) {
    // console.log('positionId', positionId);
    let url = this.URL + '/procedures/byposition/' + positionId;
    // console.log('url ', url);
    
    return this.http.get(url).pipe(
      map((resp: any) => {
        // console.log(resp.procedure);
        return resp.procedure;
      })
    );
  }

  createProcedure(procedure: {positionId: string, procedureName:string, order: number}){
    let url = this.URL + '/procedures';
    return this.http.post( url, procedure);

  }

  updateProcedure(procedure: Procedure){
    let url = this.URL + '/procedures/'+ procedure.id;
    return this.http.put( url, procedure);
  }

  deleteProcedure( id: string) {

    let url = this.URL + '/procedures/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }


}
