import { Component, OnInit } from '@angular/core';
import { AcquisitionRequest } from 'src/app/models/acquisitionRequest.model';
import { AcquisitionRequestService } from 'src/app/services/acquisitionRequest/acquisition-request.service';
import { MatDialog } from '@angular/material/dialog';
import { ProductComponent } from '../product/product.component';
import { AdmConditionComponent } from '../adm-condition/adm-condition.component';
import { Product } from 'src/app/models/product.model';
import { AdmCondition } from 'src/app/models/admCondition.model';
import { ProductService } from 'src/app/services/product/product.service';
import { AdmConditionService } from 'src/app/services/admCondition/adm-condition.service';
import { FormGroup, FormBuilder, Validators, AbstractControl } from '@angular/forms';
import { SnackService } from 'src/app/services/snack.service';
import { StatusService } from '../../services/status/status.service';
import { Status } from '../../models/status.model';
import { Store } from '@ngrx/store';
import { AppState } from '../../store/app.reducers';
import * as actions from '../../store/actions/status.actions';
import { User } from '../../models/user';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';



@Component({
  selector: 'app-acquisition-request-form',
  templateUrl: './acquisition-request-form.component.html',
  styleUrls: ['./acquisition-request-form.component.scss']
})
export class AcquisitionRequestFormComponent implements OnInit {


  public acquisitionRequest: AcquisitionRequest; // = new AcquisitionRequest("","","",null,"",null,"","","","","");
  public acquisitionRequestTest: AcquisitionRequest;
  public acquisitionRequestForm: FormGroup;
  currentStatus: Status;
  currentUser: User;
  // id: string = 'new';
  // productDialogRef: MatDialogRef<ProductComponent>;
  // admConditionDialogRef: MatDialogRef<AdmConditionComponent>;
  products: Product[] = [];
  productsTest: Product[] = [];
  admConditions: AdmCondition[] = [];
  displayedProducts: string[] = ['name', 'description', 'quantity', 'price','total', 'edit', 'delete'];
  displayedAdmConditions: string[] = ['priority', 'type', 'warranty', 'edit', 'delete'];
  constructor(
    public _acquisitionRequest: AcquisitionRequestService,
    private authService: AuthService,
    private formBuilder: FormBuilder,
    private dialog: MatDialog,
    public _product: ProductService,
    public _admCondition: AdmConditionService,
    public snackService: SnackService,
    private router: Router,
    private store: Store<AppState>
  ) { }

  ngOnInit(): void {


    // this.activatedRoute.params
    // .subscribe( ({ id }) => this.getAcquisitionRequest( id ) );

    this.currentUser =  JSON.parse(this.authService.getUser()) ;

    this.acquisitionRequestForm = this.formBuilder.group({
          code: ["GAF-08-01"],
          version: ["03"],
          validity: [new Date('2/13/2019')],
          numRequest: [null, Validators.required],
          requestDate: [null, Validators.required],
          nPac: [null, Validators.required],
          place: [null, Validators.required],
          question1: [null, Validators.required],
          question2: [null, [Validators.required, Validators.minLength(10)]],
          question3: [null, [Validators.required, Validators.minLength(10)]],
          coin: [null, Validators.required],
          title: [null, Validators.required]
        });



    this.store.select('status').subscribe(({ status }) => {
          if (status != null) {
            // console.log('el estado actual es ', status);
            this.currentStatus = status;
            if ( status.formId != null) {
              this.getAcquisitionRequest( status.formId);
            } else {
              this.getAcquisitionRequest( 'new');
            }
          }
        });

  }

  resetForm(formGroup: FormGroup) {
    let control: AbstractControl = null;
    formGroup.reset();
    formGroup.markAsUntouched();
    Object.keys(formGroup.controls).forEach((name) => {
      control = formGroup.controls[name];
      control.setErrors(null);
    });
    formGroup.setErrors({ invalid: true });
  }

  getAcquisitionRequest(id: string){
    if ( id === 'new'){

      this.acquisitionRequest = null;
      this.products = null;
      this.admConditions = null;
      this.resetForm(this.acquisitionRequestForm);
        // this.resetForm();
        // this.acquisitionRequestForm.reset(this.acquisitionRequest);
        // const {code, version, validity, numRequest, requestDate, nPac, place, question1, question2, question3} = this.acquisitionRequest;
        // this.acquisitionRequestForm.setValue({code, version, validity, numRequest, requestDate, nPac, place, question1, question2, question3});
      return;
    }else {
      this._acquisitionRequest.getACQRequest(id)
      .subscribe(acquisitionRequest => {
        // if(!acquisitionRequest){
        //   return this.router.navigateByUrl(`/acquisitionRequests`);
        // }
        if (acquisitionRequest) {
          const { admConditions, code, id, nPac, numRequest, place, products, question1, question2, question3, requestDate, validity, version, coin,title} = acquisitionRequest;
          this.acquisitionRequest = acquisitionRequest;
          this.products = acquisitionRequest.products;
          this.admConditions = acquisitionRequest.admConditions;
          this.acquisitionRequestForm.setValue({code, version, validity, numRequest, requestDate, nPac, place, question1, question2, question3, coin, title});
        }
      });
    }


  }


  save() {
    // this.currentUser =  JSON.parse(this.authService.getUser()) ;
    const {code} = this.acquisitionRequestForm.value;

    if ( this.acquisitionRequest ) {
      // update
      const data = {
        ...this.acquisitionRequestForm.value,
        id: this.acquisitionRequest.id
      };
      this._acquisitionRequest.updateAcquisitionRequest( data )
        .subscribe( (resp: any) => {

          if (this.currentStatus.procedureId) {
            const std: Status = Status.fromState( this.currentStatus );
            std.userId = this.currentUser.id;
            std.status = 'terminado';
              // console.log('current status ', std);
            this.store.dispatch( actions.updateStatus({ status: std }));
          } else {
            this.router.navigateByUrl('/acquisitionRequests');
          }
          // console.log('ssdasd ', resp.acquisitionRequest[0]);

          this.snackService.success('Se ha actualizado correctamente la solicitud de adquisicion ' + `${ code }`);

        });

    } else {
      // create

      this._acquisitionRequest.createAcquisitionRequest( this.acquisitionRequestForm.value )
          .subscribe( (resp: any) => {
            // console.log('solicitud guardada ', resp.acquisitionRequest.id );

            const std: Status = Status.fromState( this.currentStatus );
            std.formId = resp.acquisitionRequest.id;
            std.codeRequest = resp.acquisitionRequest.id;
            std.userId = this.currentUser.id;
            std.status = 'pendiente';
            // console.log('current status ', std);
            this.store.dispatch( actions.initialStatus({ status: std }));

            // this._statusService.addStatus(resp.acquisitionRequest.id, resp.acquisitionRequest.id, 'Primer estado');
            this.getAcquisitionRequest(resp.acquisitionRequest.id);
            this.snackService.success('Se ha creado correctamente la solicitud de adquisicion ' + `${code }`);

            // this.router.navigateByUrl(`/acquisitionRequest/${ resp.acquisitionRequest.id }`)
            // this.router.navigateByUrl(`/`);
        });
    }



  }


  delete( id: string ) {

    this._acquisitionRequest.deleteAcquisitionRequest( id )
            .subscribe ( () =>
            { this._acquisitionRequest.getAcquisitionRequests();
              this.snackService.warn('Se ha borrado correctamente la solicitud de adquisicion ');
            });

  }

  // product
  getProducts(){
    this._product.getProducts()
      .subscribe(products => this.products = products);
  }
  openAddProductDialog(){
    const productDialogRef = this.dialog.open(ProductComponent, {
      data: { requestId: this.acquisitionRequest.id,
              productId: 'new'
      }
    });
    productDialogRef.afterClosed().subscribe( result => {
      this.getAcquisitionRequest(this.acquisitionRequest.id);
    });

  }
  openUpdateProductDialog(id: string){
    const productDialogRef = this.dialog.open(ProductComponent, {
      data: { productId: id}
    });
    productDialogRef.afterClosed().subscribe( result => {
      this.getAcquisitionRequest(this.acquisitionRequest.id);
    });
  }
  deleteProduct( id: string ) {

    this._product.deleteProduct( id )
            .subscribe ( () =>
            { this._product.getProducts();
              this.snackService.warn('Se ha borrado correctamente el Item ');
              this.getAcquisitionRequest(this.acquisitionRequest.id);
            });

  }

  // AdmCondition
  getAdmConditions(){
    this._admCondition.getAdmConditions()
      .subscribe(admConditions => this.admConditions = admConditions);
  }
  openAddAdmConditionDialog(){
    const admConditionDialogRef = this.dialog.open(AdmConditionComponent, {
      data: { requestId: this.acquisitionRequest.id,
              admConditionId: 'new'
      }
    });
    admConditionDialogRef.afterClosed().subscribe( result => {
      this.getAcquisitionRequest(this.acquisitionRequest.id);
      if (this.currentStatus.procedureId) {
        const std: Status = Status.fromState( this.currentStatus );
        std.userId = this.currentUser.id;
        std.status = 'terminado';
          // console.log('current status ', std);
        this.store.dispatch( actions.updateStatus({ status: std }));
      } else {
        this.router.navigateByUrl('/acquisitionRequests');
      }


    });
  }

  openUpdateAdmConditionDialog(id: string){
    const admConditionDialogRef = this.dialog.open(AdmConditionComponent, {
      data: { admConditionId: id}
    });
    admConditionDialogRef.afterClosed().subscribe( result => {
      this.getAcquisitionRequest(this.acquisitionRequest.id);
    });
  }

  deleteAdmCondition( id: string ) {

    this._admCondition.deleteAdmCondition( id )
            .subscribe ( () =>
            {
              this.snackService.warn('Se ha borrado correctamente la Condicion Administrativa ');
              this.getAcquisitionRequest(this.acquisitionRequest.id);
            });

  }





}
