import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { Status } from 'src/app/models/status.model';
import { GLOBAL } from '../../config/global';
import { AuthService } from '../auth.service';
import { User } from '../../models/user';
import { ProcedureService } from '../procedure/procedure.service';
import { IStatus } from '../../interfaces/interfaces';
import { PositionService } from '../position/position.service';
import { FormGroup, FormControl, Validators, ValidatorFn, AbstractControl } from '@angular/forms';
import { Observable } from 'rxjs/internal/Observable';


@Injectable({
  providedIn: 'root'
})
export class StatusService {
  private URL = GLOBAL.uri;
  // currentProcedure: IProcedure;
  statuss: IStatus [] = [];
  currentUser: User;
  currentDate1: Date = new Date();
  currentDate2: Date = new Date();
  searchform: FormGroup;
  selectedStatus: Status;
  private user: User;

  constructor(
              private http: HttpClient,
              private authService: AuthService,
              private procedureService: ProcedureService,
              private positionService: PositionService
              ) {

                this.user = JSON.parse(this.authService.getUser());

                this.currentDate1.setSeconds(this.currentDate1.getSeconds() - this.currentDate1.getMinutes() * 60);
                this.currentDate1.setTime(this.currentDate1.getTime() - (24 * 60 * 60 * 1000) * 14);
                this.currentDate2.setSeconds(this.currentDate2.getSeconds() - this.currentDate2.getMinutes() * 60);

                if (this.user) {

                  this.searchform = new FormGroup({
                    userId: new FormControl(this.user.id, Validators.required),
                    startDate: new FormControl(this.currentDate1, [Validators.required, this.validStartDate('endDate')]),
                    endDate: new FormControl(this.currentDate2, [Validators.required, this.validEndDate('startDate')])
                  });
                }


              }

  validStartDate(field_name): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
        const input = control.value;
        // console.log('Valor dato ', control.root.value[field_name]);
        const isValid = control.root.value[field_name] > input;
        if (!isValid) {return { validStartDate: {isValid}};
        } else { return null; }

    };
  }


  validEndDate(field_name): ValidatorFn  {
    return (control: AbstractControl): { [key: string]: any } => {
        const input = control.value;
        const isValid = control.root.value[field_name] < input;
        if (!isValid) {
            return { validEndDate: {isValid}};
        }
        else {
            return null;
        }
    };
  }

  reciveStatus(row) {
    this.selectedStatus = row;
  }

  getStatuss() {
    const url = this.URL + '/status';
    return this.http.get<IStatus>(url).pipe(
      map((resp: any) => {
        return resp.status;
      })
    );
  }

  getStatusByPositionId(postionId: string, procedureId: string, userId: string){

    const url = this.URL + '/status/positionId/' + postionId + '/' + procedureId + '/' + userId;
    return this.http.get<IStatus>(url).pipe(
      map((resp: any) => {
        return resp.status;
      })
    );

  }

  getStatusByUserId(searchForm): Observable<any>{
    const url = this.URL + '/status/statusById';
    return this.http.post( url, searchForm);
  }

  getStatus(id: string) {

    const url = this.URL + '/status/' + id;
    return this.http.get<IStatus>(url).pipe(
      map((resp: any) => {
        return resp.status;
      })
    );
  }

  getStatusByRequest(codeRequest: string) {
    const url = this.URL + '/status/byrequest/' + codeRequest;
    return this.http.get<IStatus>(url).pipe(
      map((resp: any) => {
          return resp.status;
      })
    );
  }

  createStatus(status: Status) {
    const url = this.URL + '/status';
    return this.http.post( url, status);

  }

  getCurrentStatus(codeRequest: string) {
    const url = this.URL + '/status/current/' + codeRequest;
    return this.http.get<IStatus>(url).pipe(
      map((resp: any) => {
          return resp.status;
      })
    );
  }

  addProcedureStatus(status: Status, formId: string) {
    let newOrder = 0 ;
    if (status.procedureId === null ) {
       // caso 1:
        // no recibe nada se debe crear un estado inicial con current en false
        // y un nuevo estado con el siguiente id de procedimiento y  con current en true y el usuario en null

        newOrder = 1;

        this.procedureService.getProcedureByOrder(newOrder)
              .subscribe(
                currentProcedure => {
                  status.procedureId = currentProcedure.id;
                  status.name = currentProcedure.procedureName;
                  status.current = false;
                  status.userId = this.user.id;

                  this.createStatus(status).subscribe(
                    (result: any) => {
                      console.log('se ha creado correctamente el estado ', result);
                      newOrder = currentProcedure.order + 1;
                      this.procedureService.getProcedureByOrder(newOrder)
                            .subscribe( nextProcedure => {

                              console.log('siguiente procedimiento ', nextProcedure);
                              status.procedureId = nextProcedure.id;
                              status.name = nextProcedure.procedureName;
                              status.formId = null;
                              status.current = true;
                              status.userId = null;

                              this.createStatus(status). subscribe(
                                nextStatus => {
                                  console.log('Se ha creado el siguiente estado', nextStatus );
                                }, error => console.log(error)
                              );

                            }, error => console.log(error));


                    }, error => console.log(error)
                  );

                }, error => console.log(error));

      } else {

        // caso 2:
        // existe un estado que debe actualizarse al usuario actual  y current en false
        // y un nuevo estado con el siguiente id de procedimiento y  con current en true y el usuario en null

        this.procedureService.getProcedure(status.procedureId)
              .subscribe(
                currentProcedure => {
                  console.log('procedimiento actual ', currentProcedure);

                  status.current = false;
                  status.userId = this.user.id;
                  status.formId = formId;
                  status.dateAt = new Date('dd/MM/yyyy HH:mm:ss');

                  this.updateStatus(status)
                        .subscribe(
                          currentStatus => {
                            console.log('estado actualizado', currentStatus);
                          }, error => console.log(error)
                        );

                  newOrder = currentProcedure.order + 1;
                  this.procedureService.getProcedureByOrder(newOrder)
                    .subscribe(
                      res => {
                        status.procedureId = res.id;
                        status.name = res.procedureName;
                        status.current = true;
                        status.userId = null;

                        this.createStatus(status).subscribe(
                          (result: any) => {
                            console.log('se ha creado correctamente el estado ', result);

                          }, error => console.log(error)
                        );

                      }, error => console.log(error));
                }
              );

      } // fin else

  }

   addStatus(codeRequest: string, formId: string, name: string) {

    let status: Status;
    this.getCurrentStatus(codeRequest).subscribe(
      result => {
        // console.log('result', result);
        if (result === null){
          status = new Status(null, null, null, formId, 'proceso', new Date('dd/MM/yyyy HH:mm:ss'), false, codeRequest, 'estado Inicial');

          this.addProcedureStatus(status, formId);

        }else {
          status = result;
          this.addProcedureStatus(status, formId);
        }

      }, error => console.log(error)
    );


  }

  updateStatus(status: Status){
    const url = this.URL + '/status/' + status.id;
    return this.http.put( url, status);
  }

  deleteStatus( id: string) {

    const url = this.URL + '/status/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }


}
