import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { LegalContract } from 'src/app/models/legalContract.model';
import { GLOBAL } from '../../config/global';

@Injectable({
  providedIn: 'root'
})
export class LegalContractService {
  private URL = GLOBAL.uri;
  constructor(private http: HttpClient) {}

  getLegalContracts() {
    let url = this.URL + '/legalContracts';
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.legalContracts;
      })
    );
  }

  getLegalContract(id: string) {
    let url = this.URL + '/legalContracts/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.legalContract;
      })
    );
  }

  getPCD(id: string) {
    let url = this.URL + '/legalContracts/all/' + `${id}`;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.legalContract;
      })
    );
  }

  createLegalContract(legalContract: {code: string, description:string, responsable:string}){
    let url = this.URL + '/legalContracts';
    return this.http.post( url, legalContract);

  }

  updateLegalContract(legalContract: LegalContract){
    let url = this.URL + '/legalContracts/'+ legalContract.id;
    return this.http.put( url, legalContract);
  }

  deleteLegalContract( id: string) {

    let url = this.URL + '/legalContracts/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }


}
