import { Component, OnInit, SimpleChanges, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { BudgetCertification } from 'src/app/models/budgetCertification.model';
import { BudgetCertificationService } from 'src/app/services/budgetCertification/budget-certification.service';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { DocumentService } from 'src/app/services/document/document.service';
import { SnackService } from 'src/app/services/snack.service';
import { DocumentComponent } from '../document/document.component';
import { Document } from 'src/app/models/document.model';
import { ModalFileUploadService } from 'src/app/components/modal-file-upload/modal-file-upload.service';
import { ModalFileUploadComponent } from 'src/app/components/modal-file-upload/modal-file-upload.component';
import { Status } from '../../models/status.model';
import { Store } from '@ngrx/store';
import { AppState } from '../../store/app.reducers';
import * as actions from '../../store/actions/status.actions';
import { User } from '../../models/user';
import { AuthService } from '../../services/auth.service';
import { FilePreviewComponent } from '../file-preview/file-preview.component';
import { Router } from '@angular/router';


@Component({
  selector: 'app-budget-certification-form',
  templateUrl: './budget-certification-form.component.html',
  styleUrls: ['./budget-certification-form.component.scss']
})
export class BudgetCertificationFormComponent implements OnInit {

  codeRequest: string;
  currentStatus: Status;
  currentUser: User;

  public budgetCertificationForm: FormGroup;
  public budgetCertification: BudgetCertification;
  documents: Document[] = [];
  displayedDocuments: string[] = ['document', 'preview', 'name', 'edit', 'delete'];
  iconFile = '../assets/images/icon/add-file.png';

  constructor(
    public _budgetCertification: BudgetCertificationService,
    private authService: AuthService,
    private formBuilder: FormBuilder,
    private router: Router,
    private dialog: MatDialog,
    public _document: DocumentService,
    public snackService: SnackService,
    public modalUploadFileService: ModalFileUploadService,
    private store: Store<AppState>
  ) { }

  ngOnInit(): void {

    this.currentUser =  JSON.parse(this.authService.getUser()) ;



    this.budgetCertificationForm = this.formBuilder.group({
          reserveCode: [null, Validators.required],

        });

    this.store.select('status').subscribe(({ status }) => {
          if (status != null) {
            // console.log('el estado actual es ', status);
            this.currentStatus = status;
            if ( status.formId != null) {
              this.getBudgetCertification( status.formId);
            } else {
              this.getBudgetCertification( 'new');
            }
          }
        });
  }


  terminate() {
    // console.log('Documentos cargados', this.documents);
    if ( this.currentStatus.procedureId) {
      const std: Status = Status.fromState( this.currentStatus );
      if (this.documents.length > 0) {
            std.userId = this.currentUser.id;
            std.status = 'terminado';
            // console.log('current status ', std);
            this.store.dispatch( actions.updateStatus({ status: std }));
          }else {
              std.status = 'pendiente';
          }
    }
  }

  getBudgetCertification(id: string){

    if (id === 'new'){
      return;
    }
    this._budgetCertification.getBC(id)
      .subscribe(budgetCertification => {
        // if ( !budgetCertification ) {
        //   return this.router.navigateByUrl(`/budgetCertifications`);
        // }
        // console.log('form certification ', budgetCertification);
        if (budgetCertification){
          const {reserveCode} = budgetCertification;
          this.budgetCertification = budgetCertification;
          this.documents = budgetCertification.documents;
          this.budgetCertificationForm.setValue({reserveCode });
        }

      });
  }

  save() {

    const {reserveCode} = this.budgetCertificationForm.value;

    if ( this.budgetCertification ) {
      // update
      const data = {
        ...this.budgetCertificationForm.value,
        id: this.budgetCertification.id
      };
      this._budgetCertification.updateBudgetCertification( data )
        .subscribe( resp => {
          this.snackService.success('Se ha actualizado correctamente la Certificacion Presupuestaria ' + `${ reserveCode }`);
          if (!this.currentStatus.procedureId) {
            this.router.navigateByUrl('/budgetCertifications');
          } else {
            this.terminate();
          }
        });

    } else {
      // create

      this._budgetCertification.createBudgetCertification( this.budgetCertificationForm.value )
          .subscribe( (resp: any) => {
            // console.log('valor de la certificacin guardada', resp);
            if (this.currentStatus.procedureId){
              const std: Status = Status.fromState( this.currentStatus );
              std.formId = resp.budgetCertification.id;
              std.userId = this.currentUser.id;
              std.status = 'pendiente';
              this.store.dispatch( actions.updateStatus({ status: std }));
            }
            

            this.getBudgetCertification(resp.budgetCertification.id);
            this.snackService.success('Se ha creado correctamente la Certificacion Presupuestaria ' + `${reserveCode }`);

        });
    }



  }

  delete( id: string ) {

    this._budgetCertification.deleteBudgetCertification( id )
            .subscribe ( () =>
            { this._budgetCertification.getBudgetCertifications();
              this.snackService.warn('Se ha borrado correctamente la Certificacion Presupuestaria ');
            });

  }

  // document
  // getDocuments(){
  //   this._document.getDocuments()
  //     .subscribe(documents => this.documents = documents );
  // }


  openAddDocumentDialog(){
    const documentDialogRef = this.dialog.open(DocumentComponent, {
      data: { formId: this.budgetCertification.id,
              documentId: 'new'
      }
    });
    documentDialogRef.afterClosed().subscribe( result => {
      this.getBudgetCertification(this.budgetCertification.id);
    });
  }


  openUpdateDocumentDialog(id: string){
    const documentDialogRef = this.dialog.open(DocumentComponent, {
      data: { documentId: id}
    });
    documentDialogRef.afterClosed().subscribe( result => {
      this.getBudgetCertification(this.budgetCertification.id);
      // this.sendEvent();
      this.terminate();
    });
  }

  openPreviewDocumentDialog(src: string){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.width = '100%';
    dialogConfig.data = {src};
    const documentDialogRef = this.dialog.open(FilePreviewComponent, dialogConfig);

  }

  deleteDocument( id: string ) {

    this._document.deleteDocument( id )
            .subscribe ( () =>
            { this._document.getDocuments();
              this.snackService.warn('Se ha borrado correctamente el documento ');
              this.getBudgetCertification(this.budgetCertification.id);


              this.terminate();

            });

  }

  // sendEvent() {
  //   // console.log('eniva evento');
  //   this.eventRefresh.emit(true);
  // }

  createDocument(){
    const document: Document = {id:'',name:"Certificacion Presupuestaria",type: "", formId:this.budgetCertification.id,src:"./uploads",status:"active",createDate: new Date()};
    this._document.createDocument(document)
      .subscribe((resp:any) =>
      {
        this._document.getDocuments();
        this.changeFile(resp.document.id);
        this.snackService.success('Se ha creado correctamente el documento ');
        this.getBudgetCertification(this.budgetCertification.id);

        this.terminate();

      }
      );
  }

  changeFile(id: string) {
    this.dialog.open(ModalFileUploadComponent);
    this.modalUploadFileService.showModal( 'docs', id );

    this.modalUploadFileService.modalNotification
                .subscribe( (resp: any) => {
                  // console.log(resp);

                  this.changeIcon(resp.document.src);
                  this.getBudgetCertification(this.budgetCertification.id);
                  // this.sendEvent();
                  this.terminate();
                });
  }

  changeIcon(src) {

    if (src === './uploads') {
      this.iconFile = '../assets/images/icon/add-file.png';
      return;
    }
    const ext = src.split('.');
    // console.log('extencion', ext);

    if (ext[1] === 'pdf') {
      this.iconFile = '../assets/images/icon/pdf.png';
    }

  }



}
