import { Component, OnInit } from '@angular/core';
import { LegalContract } from 'src/app/models/legalContract.model';
import { LegalContractService } from 'src/app/services/legalContract/legal-contract.service';
import { SnackService } from 'src/app/services/snack.service';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { AppState } from 'src/app/store/app.reducers';
import * as actions from '../../store/actions/status.actions';

@Component({
  selector: 'app-legal-contract',
  templateUrl: './legal-contract.component.html',
  styleUrls: ['./legal-contract.component.scss']
})
export class LegalContractComponent implements OnInit {

  legalContracts: LegalContract[] = [];
  displayedColumns: string[] = ['code','description','edit','delete'];

  constructor(
    public _legalContract: LegalContractService,
    public snackService: SnackService,
    private router: Router,
    private store: Store<AppState>
  ) { }

  ngOnInit(): void {
    this.getLegalContracts();
  }

  getLegalContracts(){
    this._legalContract.getLegalContracts()
      .subscribe(legalContracts => this.legalContracts = legalContracts);
  }

  edit(id: string){
    this.store.dispatch( actions.changeFormId({ formId: id}) );
    this.router.navigateByUrl('/legalContract/edit');
  }

  delete(id: string){
    this._legalContract.deleteLegalContract(id)
      .subscribe(() =>
      {
        this.snackService.warn('Se ha borrado correctamente el Contrato Legal ');
        this.getLegalContracts();
      })
  }

}
