import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AuthService } from '../services/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  title = 'demoApp';
  email: string;
  password: string;
  remail: string;
  rpassword: string;
  rcpassword: string;
  user = {
    name: 'cosa',
    lastname: 'cosa',
    email: '',
    password: '',
    role: 'USER_ROLE',
    positionId: '3becc621-690c-4f32-8269-9dfbc2633c2f'
  };
  loginUser = {
    email: '',
    password: '',
  };

  constructor(
              private snackBar: MatSnackBar,
              private authService: AuthService,
              private router: Router
            ) { }

  ngOnInit(): void {
  }
  register() {

    this.authService.signUp(this.user)
        .subscribe(
          res => {
            // console.log(res);
            localStorage.setItem('token', res.Bearer);
            localStorage.setItem('user', res.user);
            this.snackBar.open('Se registro correctamente al usuario', 'Registro', { duration: 1000 });
            this.router.navigate(['/dashboard']);
          },
          err => {
            console.log(err);
          }
        );

      }
  login() {

    this.authService.signIn(this.loginUser)
        .subscribe(
          res => {
            // console.log('respuesta ', res);
            localStorage.setItem('token', res.Bearer);
            localStorage.setItem('user', JSON.stringify(res.user));
            this.snackBar.open('Credenciales correctos', 'Ingresar', { duration: 1000 });
            this.router.navigate(['/dashboard']);
          }, err => {
            console.log(err);
            this.snackBar.open('Login error', 'ERROR', {duration: 1000});
          }
        );
  }
}
