import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map, catchError } from 'rxjs/operators';
import { IUser } from '../interfaces/interfaces';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import * as _ from 'lodash';
import { User } from '../models/user';
import { GLOBAL } from '../config/global';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor( private http: HttpClient) { this.getStorage(); }

  private URL = GLOBAL.uri;
  user: User;
  token: string;

  form: FormGroup = new FormGroup({
    id: new FormControl(null),
    name: new FormControl('', Validators.required),
    lastname: new FormControl('', Validators.required),
    email: new FormControl('', Validators.email),
    password: new FormControl('', [Validators.required, Validators.minLength(5)]),
    role: new FormControl(''),
    positionId: new FormControl('')
  });

  getStorage() {
    if ( localStorage.getItem('token')) {
      this.token = localStorage.getItem('token');
      this.user = JSON.parse(localStorage.getItem('user'));
    } else {
      this.token = '';
      this.user = null;
    }
  }

  initializeFormGroup() {
    this.form.setValue({
      id: null,
      name: '',
      lastname: '',
      email: '',
      password: '',
      role: '',
      positionId: ''
    });
  }

  getUsers() {

    // return this.http.get<IUser[]>(this.URL + '/users').pipe(map(results => results['data']));
    return this.http.get<IUser[]>(this.URL + '/users')
            .pipe(
                map( results => {
                  return results['data'].map( user => User.UsersFromJson(user) );
                  }
                )
              );
  }

  getUser(id) {
    return this.http.get<IUser>(this.URL + '/users/' + id);
  }

  populateForm(user) {
    this.form.setValue(user);
  }

  saveUser( user: User) {
   
    return this.http.post( this.URL + '/users/signup', user).pipe(
      map( (resp: any) => {
        return resp['data'];
        })
      );
  }

  updateUser( user: User ) {

    return this.http.put( this.URL + '/users/' + user.id , user ).pipe(
        map( (resp: any)  => {
          // if ( user.id === this.user.id) {
          //   const userDB: User = resp.user;
          //   // this.user = resp.user;
          //   this.saveStorage(resp.user._id, this.token, userDB);
          // }
          // alert('Usuario actualizado');
          return true;
        }));

  }

  deleteUser( id: string ) {

    return this.http.delete( this.URL + '/users/' + id ).pipe(
      map( resp => {
        // alert('Usuario eliminado correctamente');
        return true;
      })
    );
  }

}
