import { Component, OnInit, Input } from '@angular/core';
import { BudgetCertification } from 'src/app/models/budgetCertification.model';
import { Document } from 'src/app/models/document.model';
import { BudgetCertificationService } from 'src/app/services/budgetCertification/budget-certification.service';
import { DocumentService } from 'src/app/services/document/document.service';
import { AppState } from '../../store/app.reducers';
import { Store } from '@ngrx/store';
@Component({
  selector: 'app-budget-certification-view',
  templateUrl: './budget-certification-view.component.html',
  styleUrls: ['./budget-certification-view.component.scss']
})
export class BudgetCertificationViewComponent implements OnInit {

  budgetCertification: BudgetCertification;
  documents: Document;
  id: string = null;
  displayedDocuments: string[] = ['name', 'type', 'src'];
  fileView: File;
  pdfSrc = "https://vadimdez.github.io/ng2-pdf-viewer/assets/pdf-test.pdf";
  pdfSrc1 = "../../../assets/certificacion.pdf";
  pdfSrc2 = "http://localhost:3000/api/files/19d3b9d2-f2ad-413f-b10e-0ca4befc5be3-544.pdf"
  constructor(
    public _budgetCertification: BudgetCertificationService,
    public _document: DocumentService,
    private store: Store<AppState>

  ) { }

  ngOnInit(): void {
    

    this.store.select('status').subscribe(({ status }) => {
      console.log('el estado en la certificacin es ', status);
      this.id = status.formId;
      this.getBudgetCertification(this.id);
    });

  }

  getBudgetCertification(id: string){
    this._budgetCertification.getBC(id)
      .subscribe(budgetCertification => {
        if ( !budgetCertification ) {
          return;
        }

        this.budgetCertification = budgetCertification;
        this.documents = budgetCertification.documents;


      })
  }

  getFile(src){
    this._document.getFile(src)
    .subscribe(resp => {
      console.log(this.fileView);
      this.fileView = resp;
      console.log(this.fileView);
    })
  }

}
