import { createAction, props } from '@ngrx/store';
import { Status } from '../../models/status.model';

export const loadStatus = createAction(
    '[Status] Load statuss',
    props<{ id: string }>()
    );
export const loadStatusSuccess = createAction(
    '[Status] Load status Success',
    props<{ status: Status }>()
);

export const loadStatusError = createAction(
    '[Status] Load status Error',
    props<{ payload: any }>()
);

export const resetStatus = createAction(
    '[Status] Reset status'
);

export const requestStatus = createAction(
    '[Status] Initial Request status',
    props<{ userId: string, procedureId: string, name: string }>()
);

export const changeFormId = createAction(
    '[Status] Change Form Id',
    props<{ formId: string }>()
);

export const initialStatus = createAction(
    '[Status] Create Initial status',
    props<{ status: Status }>()
);

export const initialStatusSuccess = createAction(
    '[Status] create initial status Success',
    props<{ status: Status }>()
);

export const initialStatusError = createAction(
    '[Status] create initial status Error',
    props<{ payload: any }>()
);

export const createStatus = createAction(
    '[Status] Create status',
    props<{ status: Status }>()
);

export const createStatusSuccess = createAction(
    '[Status] create status Success',
    props<{ status: Status }>()
);

export const createStatusError = createAction(
    '[Status] create status Error',
    props<{ payload: any }>()
);

export const updateStatus = createAction(
    '[Status] Update status',
    props<{ status: Status }>()
);

export const updateStatusSuccess = createAction(
    '[Status] Update status Success',
    props<{ status: Status }>()
);

export const updateStatusError = createAction(
    '[Status] Update status Error',
    props<{ payload: any }>()
);
