import { Component, OnInit } from '@angular/core';
import { SnackService } from '../../../services/snack.service';
import { MatDialogRef } from '@angular/material/dialog';
import { Product } from '../../../models/product.model';
import { AdmCondition } from '../../../models/admCondition.model';
import { AcquisitionRequest } from '../../../models/acquisitionRequest.model';
import { StatusService } from '../../../services/status/status.service';
import { Status } from '../../../models/status.model';
import { AcquisitionRequestService } from '../../../services/acquisitionRequest/acquisition-request.service';
import { PositionService } from '../../../services/position/position.service';
import { OfficeService } from '../../../services/office/office.service';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

  displayedProducts: string[] = ['name', 'description', 'quantity', 'price'];
  displayedAdmConditions: string[] = ['priority', 'type', 'warranty'];
  acquisitionRequest: AcquisitionRequest;
  products: Product;
  admConditions: AdmCondition;
  status: Status;
  currentOffice: string;
  constructor(
    public snackService: SnackService,
    private statusService: StatusService,
    private positionService: PositionService,
    private officeService: OfficeService,
    public acquisitionRequestService: AcquisitionRequestService,
    public dialogRef: MatDialogRef<DetailComponent>
  ) { }

  ngOnInit(): void {
    this.status = this.statusService.selectedStatus;
    console.log('estado seleccionado ', this.status)
    this.getAcquisitionRequest(this.status.codeRequest);
    this.getCurrentState(this.status.codeRequest);
  }

  getAcquisitionRequest(id: string){
    this.acquisitionRequestService.getACQRequest(id)
      .subscribe(acquisitionRequest => {
        if (!acquisitionRequest){
          return;
        }
        this.acquisitionRequest = acquisitionRequest;
        this.products = acquisitionRequest.products;
        this.admConditions = acquisitionRequest.admConditions;

      });
  }

  onClose(){
    // this.userService.form.reset();
    // this.userService.initializeFormGroup();
    this.dialogRef.close();
  }
  
  getCurrentState(codeRequest){
    this.statusService.getCurrentStatus(codeRequest).subscribe( currentStatus => {
      console.log('actual estado', currentStatus);
      this.positionService.getPosition(currentStatus.procedure.positionId).subscribe(position => {
          this.officeService.getOffice(position.officeId).subscribe(office => {
            this.currentOffice = office.name;
          });
      })
    });
    // this._position.getPositions().subscribe(positions => this.positions = positions);
  }
}
