import { Component, OnInit, Inject } from '@angular/core';
import { Position } from 'src/app/models/position.model';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { PositionService } from 'src/app/services/position/position.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { SnackService } from 'src/app/services/snack.service';


@Component({
  selector: 'app-position-form',
  templateUrl: './position-form.component.html',
  styleUrls: ['./position-form.component.scss']
})
export class PositionFormComponent implements OnInit {

  position: Position;
  public positionForm: FormGroup;
  officeId: string;
  positionId: string;
  constructor(
    public _position: PositionService,
    private positionFormBuilder: FormBuilder,
    private dialogRef: MatDialogRef<PositionFormComponent>,
    @Inject(MAT_DIALOG_DATA) public data: PositionFormComponent,
    public snackService: SnackService,
  ) { }

  ngOnInit(): void {
    if(this.data.positionId !== 'new'){
      this.getPosition(this.data.positionId);
    }

    this.positionForm = this.positionFormBuilder.group({
      name:[null, Validators.required],
      officeId:[this.data.officeId],
      active:[null, Validators.required],

    });
  }
  getPosition(id: string){
    this._position.getPosition(id)
      .subscribe(position => {
        if(!position){
          this.onCloseDialog();
        }
        const {name, officeId, active} = position;
        this.position = position;
        this.positionForm.setValue({name, officeId, active });
      })
  }
  onCloseDialog(){
    this.dialogRef.close();
  }

  save() {

    const {name} = this.positionForm.value;
    if ( this.position ) {
      // update
      const data = {
        ...this.positionForm.value,
        id: this.position.id,
        officeId: this.position.officeId
      }

      this._position.updatePosition( data )
        .subscribe( resp => {
          this.snackService.success('Se ha actualizado correctamente el cargo ' + `${ name }`);
          this.onCloseDialog();
        })

    } else {
      // create

      this._position.createPosition( this.positionForm.value )
          .subscribe( (resp: any) => {
            this.snackService.success('Se ha creado correctamente el cargo '+ `${name }`);
            this.onCloseDialog();
        })
    }



  }


}
