import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { AdmCondition } from 'src/app/models/admCondition.model';
import { GLOBAL } from '../../config/global';
@Injectable({
  providedIn: 'root'
})
export class AdmConditionService {
  private URL = GLOBAL.uri;
  constructor(private http: HttpClient) { }

  getAdmConditions() {
    let url = this.URL + '/admConditions';
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.admConditions;
      })
    );
  }

  getAdmCondition(id: string) {
    let url = this.URL + '/admConditions/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.admCondition;
      })
    );
  }

  getAdmConditionRequest(id: string) {
    let url = this.URL + '/admConditions/request/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.admCondition;
      })
    );
  }

  createAdmCondition(admCondition: {priority: string, type:string, warranty: string, deliveryTime: number, placeDelivery: string, posibleProviders: string, requestId: string}){
    let url = this.URL + '/admConditions';
    return this.http.post( url, admCondition);

  }

  updateAdmCondition(admCondition: AdmCondition){
    let url = this.URL + '/admConditions/'+admCondition.id;
    return this.http.put( url, admCondition);
  }

  deleteAdmCondition(id: string){
    let url = this.URL + '/admConditions/'+ id;
    return this.http.delete(url);
  }
}
