import { Component, OnInit, Inject } from '@angular/core';
import { Document } from 'src/app/models/document.model';
import { DocumentService } from 'src/app/services/document/document.service';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DocumentComponent } from 'src/app/pages/document/document.component';
import { SnackService } from 'src/app/services/snack.service';


@Component({
  selector: 'app-file-preview',
  templateUrl: './file-preview.component.html',
  styleUrls: ['./file-preview.component.scss']
})
export class FilePreviewComponent implements OnInit {

  url: string;
  src: string;
  formId: string;
  document: Document;
  aux = '518ddd2a-e9b6-459a-8fae-01665ef0661f-788.pdf';

  constructor(
    public _document: DocumentService,
    private dialogRef: MatDialogRef<FilePreviewComponent>,
    @Inject(MAT_DIALOG_DATA) public data: FilePreviewComponent,
    public snackService: SnackService,
  ) { }

  ngOnInit(): void {
    if(this.data.src){
      this.getFileSrc(this.data.src);
    }
  }
  getDocument(id: string){
    this._document.getDocumentForm(id)
      .subscribe(document => {
        if(!document){
          this.onCloseDialog();
        }

        this.document = document;
        this.getFileSrc(this.document.src);
        console.log(this.url);


      })
  }
  getFileSrc(src: string){
    if(src=="./uploads"){
      this.onCloseDialog();
      this.snackService.warn("No se tiene ningun archivo");
    }
    else{

      this.url = src;

    }
  }
  onCloseDialog(){

    this.dialogRef.close();
  }
}
