import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import * as statusActions from '../actions/statuss.actions';
import { tap, mergeMap, map, catchError } from 'rxjs/operators';
import { StatusService } from '../../services/status/status.service';
import { of } from 'rxjs';


@Injectable()
export class StatussEffects {


    constructor(
        private actions$: Actions,
        private statusService: StatusService
    ) {}
    loadStatuss$ = createEffect(
        () => this.actions$.pipe(
           ofType(statusActions.loadStatuss),
        //    tap( data => console.log('effect tap ', data) ),
           mergeMap(
               ( action ) => this.statusService.getStatusByPositionId(action.positionId, action.procedureId, action.userId)
                    .pipe(
                        map( status => statusActions.loadStatussSuccess({ statuss: status }) ),
                        catchError( err => of(statusActions.loadStatussError({ payload: err })) )
                    )
           )
        )
    );
}