import { Component, OnInit, ViewChild, SimpleChanges } from '@angular/core';
import { StatusService } from '../../../services/status/status.service';
import { Status } from 'src/app/models/status.model';
import { IStatus } from '../../../interfaces/interfaces';
import { ProcedureService } from '../../../services/procedure/procedure.service';
import { PositionService } from '../../../services/position/position.service';
import { AuthService } from '../../../services/auth.service';
import { User } from '../../../models/user';
import { MatPaginator } from '@angular/material/paginator';
import { Store } from '@ngrx/store';
import { AppState } from '../../../store/app.reducers';
// import { loadStatuss } from '../../../store/actions/statuss.actions';
import * as actions from '../../../store/actions/statuss.actions';

// import { loadStatus } from 'src/app/store/actions';
import { loadStatus, requestStatus } from '../../../store/actions/status.actions';

@Component({
  selector: 'app-tasks',
  templateUrl: './tasks.component.html',
  styleUrls: ['./tasks.component.scss']
})
export class TasksComponent implements OnInit {

  // @Output() statusSelect = new EventEmitter<Status>();
  filters: actions.validFilters [] = ['proceso', 'pendiente', 'terminado'];
  @ViewChild(MatPaginator) paginator: MatPaginator;
  user: User;
  statuss: Status [] = [];
  data: Status [] = [];
  status: Status;
  currentUser: User;
  refresh = false;
  currentFilter: actions.validFilters = 'proceso';

  loading: boolean = false;
  error: any;


  page = 0;
  size = 5;

  constructor(
    public statusService: StatusService,
    public procedureService: ProcedureService,
    public positionService: PositionService,
    public authService: AuthService,
    private store: Store<AppState>

  ) {}

  ngOnInit(): void {

    // this.store.select('statuss').subscribe(({ filter }) => {
    //   // console.log('el estado en filtro es ', filter);
    //   this.currentFilter = filter;

    // });
    this.getStatus();
  }

  newRequest() {
    this.user = JSON.parse(this.authService.getUser());
    this.procedureService.getProcedureByOrder(1).subscribe( procedure => {
      // console.log('Procedimento almacenado ', procedure);
      this.store.dispatch( requestStatus({ userId: this.user.id, procedureId: procedure.id, name: procedure.procedureName }) );
    });
  }

  changeFilter( filter: actions.validFilters ) {
    // console.log('filtro seleccionado ', filter);
    this.store.dispatch( actions.setFilterStatus({ filter }));
  }
  // ngOnChanges(changes: SimpleChanges): void {

  //   if (changes.refresh.currentValue){
  //     this.getStatus();
  //   }
  // }

  // doRefresh(value: boolean){
  //   console.log('list refresh ', value);
  //   if (value) {
  //     this.statuss = [];
  //     this.getStatus();
  //   }
  // }

  getStatus() {


    this.store.select('statuss').subscribe( ({statuss, loading, error, filter}) => {

      let res = statuss.filter( status => status.status === filter);
      this.statuss = res;
      this.data = this.statuss;
      this.loading = loading;
      this.error = error;
      this.currentFilter = filter;
      this.getData({pageIndex: this.page, pageSize: this.size});
    });

    this.currentUser =  JSON.parse(this.authService.getUser()) ;

    this.procedureService.getProcedureByOrder(1).subscribe( procedure => {
      this.store.dispatch( actions.loadStatuss({
              positionId: this.currentUser.positionId,
              procedureId: procedure.id,
              userId: this.currentUser.id
            }));
    });

    

  }


  // getStatus(){
  //   console.log('Carga datos de la lista de tareas');

  //   this.currentUser =  JSON.parse(this.authService.getUser()) ;

  //   this.positionService.getPosition(this.currentUser.positionId).subscribe(position => {

  //     this.procedureService.getProcedureByPosition(position.id).subscribe(procedures => {

  //       procedures.forEach( procedure => this.statuss.push(...procedure.status));

  //       this.data = this.statuss;
  //       this.getData({pageIndex: this.page, pageSize: this.size});



  //     });

  //   });

  // }



// opciones para el paginador
  getData(obj) {
    let index = 0,
    startingIndex = obj.pageIndex * obj.pageSize,
    endingIndex = startingIndex + obj.pageSize;

    this.data = this.statuss.filter(() => {
      index++;
      return (index > startingIndex && index <= endingIndex) ? true : false;
    });
  }

  sendStatus(status: Status): void {
    // console.log('estado enviado ', status);
    this.store.dispatch( loadStatus({ id: status.id }));
    this.status = status;
    this.refresh = false;
    // this.statusSelect.emit(status);
  }
}
