import { Component, OnInit } from '@angular/core';
import { ProposalEvaluation } from 'src/app/models/proposalEvaluation.model';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ProposalProvider } from 'src/app/models/proposalProvider.model';
import { ProposalProviderService } from 'src/app/services/proposalProvider/proposal-provider.service';
import { MatDialog } from '@angular/material/dialog';
import { ProposalEvaluationService } from 'src/app/services/proposalEvaluation/proposal-evaluation.service';
import { SnackService } from 'src/app/services/snack.service';
import { ModalFileUploadService } from 'src/app/components/modal-file-upload/modal-file-upload.service';
import { Router, ActivatedRoute } from '@angular/router';
import { DocumentComponent } from '../document/document.component';
import { ModalFileUploadComponent } from 'src/app/components/modal-file-upload/modal-file-upload.component';
import { Status } from '../../models/status.model';
import * as actions from '../../store/actions/status.actions';
import { User } from '../../models/user';
import { AuthService } from '../../services/auth.service';
import { Store } from '@ngrx/store';
import { AppState } from '../../store/app.reducers';

@Component({
  selector: 'app-proposal-evaluation-form',
  templateUrl: './proposal-evaluation-form.component.html',
  styleUrls: ['./proposal-evaluation-form.component.scss']
})
export class ProposalEvaluationFormComponent implements OnInit {
  codeRequest: string;
  currentStatus: Status;
  currentUser: User;
  public proposalEvaluationForm: FormGroup;
  public proposalEvaluation: ProposalEvaluation;
  proposalProviders: ProposalProvider [] = [];
  displayedProposalProviders: string[] = ['name', 'nit', 'edit', 'delete'];

  iconFile = '../assets/images/icon/add-file.png';

  constructor(
   public _proposalProvider: ProposalProviderService,
   private authService: AuthService,
   private formBuilder: FormBuilder,
   private router: Router,
   private dialog: MatDialog,
   public _proposalEvaluation: ProposalEvaluationService,
   public snackService: SnackService,
   public modalFileUploadService: ModalFileUploadService,
   private store: Store<AppState>
  ) { }

  ngOnInit(): void {

  this.currentUser =  JSON.parse(this.authService.getUser());
  this.proposalEvaluationForm = this.formBuilder.group({
    code: [null, Validators.required],

  });

  this.store.select('status').subscribe(({ status }) => {
    if (status != null) {
      // console.log('el estado actual es ', status);
      this.currentStatus = status;
      if ( status.formId != null) {
        this.getProposalEvaluation( status.formId);
      } else {
        this.getProposalEvaluation( 'new');
      }
    }
  });


}

getProposalEvaluation(id: string){

  if (id === 'new'){
    return;
  }
  this._proposalEvaluation.getPEP(id)
    .subscribe(proposalEvaluation => {
      // if ( !proposalEvaluation ) {
      //   return this.router.navigateByUrl(`/proposalEvaluations`);
      // }
      if ( proposalEvaluation ){
      const {code} = proposalEvaluation;
      this.proposalEvaluation = proposalEvaluation;

      this.proposalProviders = proposalEvaluation.proposalProviders;

      this.proposalEvaluationForm.setValue({code });
      }

    });
}


save() {

  const {reserveCode} = this.proposalEvaluationForm.value;

  if ( this.proposalEvaluation ) {
    // update
    const data = {
      ...this.proposalEvaluationForm.value,
      id: this.proposalEvaluation.id
    };
    this._proposalEvaluation.updateProposalEvaluation( data )
      .subscribe( resp => {
        this.snackService.success('Se ha actualizado correctamente la Evaluacion ' + `${ reserveCode }`);
        if (!this.currentStatus.procedureId) {
          this.router.navigateByUrl('/budgetCertifications');
        } else {
          // analizar la forma cambiar de estado cuando modifica
          // this.terminate();
        }
      });

  } else {
    // create

    this._proposalEvaluation.createProposalEvaluation( this.proposalEvaluationForm.value )
        .subscribe( (resp: any) => {
          // console.log('valor de la certificacin guardada', resp);

          if (this.currentStatus.procedureId){
            const std: Status = Status.fromState( this.currentStatus );
            std.formId = resp.proposalEvaluation.id;
            std.userId = this.currentUser.id;
            std.status = 'pendiente';
              // console.log('current status ', std);
            this.store.dispatch( actions.updateStatus({ status: std }));
          }

          this.getProposalEvaluation(resp.proposalEvaluation.id);
          this.snackService.success('Se ha creado correctamente la Evaluacion ' + `${reserveCode }`);

          // this.router.navigateByUrl(`/proposalEvaluation/${ resp.proposalEvaluation.id }`)
      });
  }



}

delete( id: string ) {

  this._proposalEvaluation.deleteProposalEvaluation( id )
          .subscribe ( () =>
          { this._proposalEvaluation.getProposalEvaluations();
            this.snackService.warn('Se ha borrado correctamente la Certificacion Presupuestaria ');
          });

}


openAddDocumentDialog(){
  const documentDialogRef = this.dialog.open(DocumentComponent, {
    data: { formId: this.proposalEvaluation.id,
            documentId: 'new'
    }
  });
  documentDialogRef.afterClosed().subscribe( result => {

    this.getProposalEvaluation(this.proposalEvaluation.id);


  });
}
openUpdateDocumentDialog(id: string){
  const documentDialogRef = this.dialog.open(DocumentComponent, {
    data: { documentId: id}
  });
  documentDialogRef.afterClosed().subscribe( result => {
    this.getProposalEvaluation(this.proposalEvaluation.id);
    console.log('cierra dialog')
  });
}


changeFile(id: string) {
  this.dialog.open(ModalFileUploadComponent);
  this.modalFileUploadService.showModal( 'docs', id );

  this.modalFileUploadService.modalNotification
              .subscribe( (resp: any) => {
                // console.log(resp);

                this.changeIcon(resp.document.src);
                this.getProposalEvaluation(this.proposalEvaluation.id);
              });
}

changeIcon(src) {

  if (src === './uploads') {
    this.iconFile = '../assets/images/icon/add-file.png';
    return;
  }
  const ext = src.split('.');
  // console.log('extencion', ext);

  if (ext[1] === 'pdf') {
    this.iconFile = '../assets/images/icon/pdf.png';
  }


}

}
