
export class User {

    static UsersFromJson( obj: Object ) {
        return new User(
            obj['id'],
            obj['name'],
            obj['lastname'],
            obj['email'],
            obj['password'],
            obj['role'],
            obj['positionId'],
            );
    }


    constructor(
                public id: string,
                public name: string,
                public lastname: string,
                public email: string,
                public password: string,
                public role: string,
                public positionId: string,

    ) {}

    get fullName() {
        return `${ this.name } ${ this.lastname }`;
    }
}