import { Component, OnInit } from '@angular/core';
import { UserService } from '../../../services/user.service';
import { MatDialogRef } from '@angular/material/dialog';
import { SnackService } from '../../../services/snack.service';
import { Position } from 'src/app/models/position.model';
import { PositionService } from 'src/app/services/position/position.service';


@Component({
  selector: 'app-users-form',
  templateUrl: './users-form.component.html',
  styleUrls: ['./users-form.component.scss']
})
export class UsersFormComponent implements OnInit {

  public positions: Position[] = [];

  public roles = [
    {value: 'ADMIN_ROLE', viewValue: 'ADMIN_ROLE'},
    {value: 'USER_ROLE', viewValue: 'USER_ROLE'}
  ];

  constructor(
              public userService: UserService,
              public snackService: SnackService,
              public dialogRef: MatDialogRef<UsersFormComponent>,
              public _position: PositionService,
              ) {

               }

  ngOnInit(): void {
    this.getPositions();
  }

  onSubmit() {

    if (this.userService.form.valid) {
      if (!this.userService.form.get('id').value) {
        this.userService.saveUser(this.userService.form.value).subscribe( resp =>  this.snackService.success(':: Se ha creado correctamente al usuario'));
       }
      else {
      this.userService.updateUser(this.userService.form.value).subscribe( resp => this.snackService.success(':: Se editado correctamente al usuario'));
      }
      this.userService.form.reset();
      this.userService.initializeFormGroup();

      this.onClose();
    }
  }

  onClear() {
    this.userService.form.reset();
    this.userService.initializeFormGroup();
    this.snackService.success(':: Se limpio el formulario');
  }

  onClose(){
    this.userService.form.reset();
    this.userService.initializeFormGroup();
    this.dialogRef.close();
  }

  getPositions(){
    this._position.getPositions().subscribe(positions => this.positions = positions);
  }
}
