import { createReducer, on } from '@ngrx/store';
import { loadStatuss, loadStatussSuccess, loadStatussError, setFilterStatus, validFilters } from '../actions';
import { Status } from '../../models/status.model';



export interface StatussState {
    positionId : string,
    statuss : Status[],
    filter : validFilters,
    loaded  : boolean,
    loading : boolean,
    error   : any
}

export const statessInitialState: StatussState = {
    positionId: null,
    statuss : [],
    filter : 'proceso',
    loaded  : false,
    loading : false,
    error   : null
}

const _statussReducer = createReducer(statessInitialState,

    on(loadStatuss, (state, { positionId }) => ({
        ...state,
        loading: true,
        positionId: positionId
    })),

    on(loadStatussSuccess, (state, { statuss }) => ({
        ...state,
        loading: false,
        loaded: true,
        statuss: [ ...statuss ]
    })),
    on(loadStatussError, (state, { payload }) => ({
        ...state,
        loading: false,
        loaded: false,
        error: {
            url: payload.url,
            name: payload.name,
            message: payload.message
        }
    })),
    on(setFilterStatus, (state, { filter }) => ({
        ...state,
        loaded: true,
        filter
    })),

);

export function statussReducer(state, action) {
    return _statussReducer(state, action);
}
