import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { Document } from 'src/app/models/document.model';
import { SnackService } from '../snack.service';
import { MatDialog } from '@angular/material/dialog';
import { ModalFileUploadService } from 'src/app/components/modal-file-upload/modal-file-upload.service';
import { ModalFileUploadComponent } from 'src/app/components/modal-file-upload/modal-file-upload.component';
import { Store } from '@ngrx/store';
import { AppState } from '../../store/app.reducers';
import * as actions from '../../store/actions/status.actions';
import { Status } from '../../models/status.model';
import { UploadFileService } from '../upload-file/upload-file.service';
import { GLOBAL } from '../../config/global';


@Injectable({
  providedIn: 'root'
})
export class DocumentService {
  private URL = GLOBAL.uri;
  iconFile = '../assets/images/icon/add-file.png';
  currentStatus: Status;
  constructor(
    private http: HttpClient,
    public snackService: SnackService,
    private dialog: MatDialog,
    public modalFileUploadService: ModalFileUploadService,
    private uploadFileService: UploadFileService,
    private store: Store<AppState>
    ) { 
      
      this.store.select('status').subscribe(({ status }) => {
        if (status != null) {
          // console.log('el estado actual es ', status);
          this.currentStatus = status;
        }
      });

    }
  getDocuments() {
    let url = this.URL + '/documents';
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.documents;
      })
    );
  }

  getDocument(id: string) {
    let url = this.URL + '/documents/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.document;
      })
    );
  }

  getDocumentForm(id: string) {
    let url = this.URL + '/documents/form/' + `${id}`;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.documents;
      })
    );
  }

  createDocument(document: {name: string, type:string, formId: string, src: string, status: string, createDate: Date}){
    let url = this.URL + '/documents';
    return this.http.post( url, document);

  }

  updateDocument(document: Document){
    let url = this.URL + '/documents/'+ document.id;
    return this.http.put( url, document);
  }

  deleteDocument( id: string) {

    let url = this.URL + '/documents/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }

  getFile(src){
    let url = this.URL + '/files/' + `${src}`;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp;
      })
    );
  }

  changeFile(id: string) {
    this.dialog.open(ModalFileUploadComponent);
    this.modalFileUploadService.showModal( 'docs', id );

    this.modalFileUploadService.modalNotification
                .subscribe( (resp: any) => {
                  console.log(resp.document.src);

                  return resp.document.src;

                });
    this.dialog.afterAllClosed.subscribe( () => {
      // actualiza el estado si subio el archivo
      
      if ( this.uploadFileService.uploaded ) {
            const std: Status = Status.fromState( this.currentStatus );
            std.status = 'terminado';
            // console.log('current status ', std);
            this.store.dispatch( actions.updateStatus({ status: std }));
         }
    });
  }

  changeIcon(src) {

    if (src == "./uploads") {
      this.iconFile = '../assets/images/icon/add-file.png';
      return this.iconFile;
    }
    const ext = src.split('.');
    // console.log('extencion', ext);

    if (ext[1] === 'pdf') {
      this.iconFile = '../assets/images/icon/pdf.png';
      }

    return this.iconFile;


  }

}
