import { Component, OnInit, ViewChild } from '@angular/core';
import { UserService } from '../../services/user.service';
import { IUser } from '../../interfaces/interfaces';
// import { MatTableDataSource, MatSort, MatPaginator } from '@angular/material';
import { MatTableDataSource } from '@angular/material/table';

import { MatDialog, MatDialogConfig, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { UsersFormComponent } from './users-form/users-form.component';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { SnackService } from '../../services/snack.service';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss']
})
export class UsersComponent implements OnInit {

  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['name', 'lastname', 'email', 'role', 'actions'];
  // columnsToDisplay: string[] = this.displayedColumns.slice();
  users: IUser[] = [];
  searchKey: string;
  showPass = true;

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  constructor(
              public dialog: MatDialog,
              public snackService: SnackService,
              private userService: UserService,
    ) {}

  ngOnInit(): void {

    this.getUsers();

  }

  getUsers() {
    this.userService.getUsers()
    .subscribe(
      res => {
        this.users = res;
        // console.log(this.users);
      },
      err => console.error(err)
    );
  }

  editUser(row): void {
    this.showPass = false;
    // console.log(row);
    this.userService.populateForm(row);
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '60%';
    const dialogRef = this.dialog.open(UsersFormComponent, dialogConfig);

    dialogRef.afterClosed().subscribe(result => {
      this.getUsers();
    });
  }

  createUser() {
    this.showPass = true;
    this.userService.initializeFormGroup();
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '60%';
    const dlgRef = this.dialog.open(UsersFormComponent, dialogConfig);
    dlgRef.afterClosed().subscribe(result => {
      this.getUsers();
    });
  }
  applyFilter(){}
  onSearchClear(){}

  delete(id): void {
    if (confirm('Esta seguro que quiere eliminar al usuario ?')){
      this.userService.deleteUser(id).subscribe( resp => {
        console.log('se elimin correctamente al usuario');
        this.getUsers();
      });
      this.snackService.warn('Se ha borrado correctamente al usuario');
      }
  }



}
