import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { TechnicalEvaluation } from 'src/app/models/technicalEvaluation.model';
import { TechnicalEvaluationService } from 'src/app/services/technicalEvaluation/technical-evaluation.service';
import { StatusService } from 'src/app/services/status/status.service';
import { Router, ActivatedRoute } from '@angular/router';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { DocumentService } from 'src/app/services/document/document.service';
import { SnackService } from 'src/app/services/snack.service';
import { ModalFileUploadService } from 'src/app/components/modal-file-upload/modal-file-upload.service';
import { DocumentComponent } from '../document/document.component';
import { Document } from 'src/app/models/document.model';
import { ModalFileUploadComponent } from 'src/app/components/modal-file-upload/modal-file-upload.component';
import { FilePreviewComponent } from '../file-preview/file-preview.component';
import { User } from '../../models/user';
import * as actions from '../../store/actions/status.actions';
import { Status } from '../../models/status.model';
import { AuthService } from '../../services/auth.service';
import { Store } from '@ngrx/store';
import { AppState } from '../../store/app.reducers';

@Component({
  selector: 'app-technical-evaluation-form',
  templateUrl: './technical-evaluation-form.component.html',
  styleUrls: ['./technical-evaluation-form.component.scss']
})
export class TechnicalEvaluationFormComponent implements OnInit {

  codeRequest: string;
  currentStatus: Status;
  currentUser: User;

  // technicalEvaluation: TechnicalEvaluation= new TechnicalEvaluation("","","","","","","","","","");
  public technicalEvaluationForm: FormGroup;
  public technicalEvaluation: TechnicalEvaluation;
  // documentDialogRef: MatDialogRef<DocumentComponent>;
  documents: Document[] = [];
  displayedDocuments: string[] = ['document', 'preview', 'name', 'edit', 'delete'];
  iconFile = '../assets/images/icon/add-file.png';
  constructor(
    public _technicalEvaluation: TechnicalEvaluationService,
    private authService: AuthService,
    private formBuilder: FormBuilder,
    private router: Router,
    private dialog: MatDialog,
    public _document: DocumentService,
    public snackService: SnackService,
    public modalUploadFileService: ModalFileUploadService,
    private store: Store<AppState>

  ) {   }

  ngOnInit(): void {

    this.currentUser =  JSON.parse(this.authService.getUser()) ;
    this.technicalEvaluationForm = this.formBuilder.group({
          to: [null, Validators.required],
          via: [null, Validators.required],
          whose: [null, Validators.required],
          number: [null, Validators.required],
          date: [null, Validators.required],

        });
    this.store.select('status').subscribe(({ status }) => {
          if (status != null) {
            // console.log('el estado actual es ', status);
            this.currentStatus = status;
            if ( status.formId != null) {
              this.getTechnicalEvaluation( status.formId);
            } else {
              this.getTechnicalEvaluation( 'new');
            }
          }
        });

  }

 /*  ngOnChanges(changes: SimpleChanges): void {
    if (changes.status.currentValue){
      this.codeRequest = this.status.codeRequest;
      // console.log('el estado recibido', this.status);
    }

  } */
  getTechnicalEvaluation(id: string){

    if (id === 'new'){
      return;
    }
    this._technicalEvaluation.getTED(id)
      .subscribe(technicalEvaluation => {
        // if ( !technicalEvaluation ) {
        //   return this.router.navigateByUrl(`/technicalEvaluations`);
        // }
        if ( technicalEvaluation ) {
        const {to, via, whose, number, date } = technicalEvaluation;
        this.technicalEvaluation = technicalEvaluation;
        this.documents = technicalEvaluation.documents;
        this.technicalEvaluationForm.setValue({to, via, whose, number, date });
        }
      });
  }

  terminate() {
    // console.log('Documentos cargados', this.documents);
   if ( this.currentStatus.procedureId ) {
    const std: Status = Status.fromState( this.currentStatus );
    if (this.documents.length > 0) {
           std.userId = this.currentUser.id;
           std.status = 'terminado';
           // console.log('current status ', std);
           this.store.dispatch( actions.updateStatus({ status: std }));
         }else {
            std.status = 'pendiente';
         }
    }
  }

  save() {

    const {number} = this.technicalEvaluationForm.value;

    if ( this.technicalEvaluation ) {
      // update
      const data = {
        ...this.technicalEvaluationForm.value,
        id: this.technicalEvaluation.id
      };
      this._technicalEvaluation.updateTechnicalEvaluation( data )
        .subscribe( resp => {
          this.snackService.success('Se ha actualizado correctamente la Evaluacion Tecnica ' + `${ number }`);
          if (!this.currentStatus.procedureId) {
            this.router.navigateByUrl('/technicalEvaluations');
          } else {
            this.terminate();
          }
        });

    } else {
      // create

      this._technicalEvaluation.createTechnicalEvaluation( this.technicalEvaluationForm.value )
          .subscribe( (resp: any) => {
            // this._statusService.addStatus(this.codeRequest, resp.technicalEvaluation.id, 'Certificacin');
            if ( this.currentStatus.procedureId ) {
              const std: Status = Status.fromState( this.currentStatus );
              std.formId = resp.technicalEvaluation.id;
              std.userId = this.currentUser.id;
              std.status = 'pendiente';
              // console.log('current status ', std);
              this.store.dispatch( actions.updateStatus({ status: std }));

              this.getTechnicalEvaluation(resp.technicalEvaluation.id);
              this.snackService.success('Se ha creado correctamente la Evaluacion Tecnica ' + `${number }`);
              }
            // this.router.navigateByUrl(`/technicalEvaluation/${ resp.technicalEvaluation.id }`)
        });
    }



  }

  delete( id: string ) {

    this._technicalEvaluation.deleteTechnicalEvaluation( id )
            .subscribe ( () =>
            { this._technicalEvaluation.getTechnicalEvaluations();
              this.snackService.warn('Se ha borrado correctamente la Evaluacion Tecnica ');
            });

  }

  // document
  getDocuments(){
    this._document.getDocuments()
      .subscribe(documents => this.documents = documents);
  }
  openAddDocumentDialog(){
    const documentDialogRef = this.dialog.open(DocumentComponent, {
      data: { formId: this.technicalEvaluation.id,
              documentId: 'new'
      }
    });
    documentDialogRef.afterClosed().subscribe( result => {
      this.getTechnicalEvaluation(this.technicalEvaluation.id);

    });
  }
  openUpdateDocumentDialog(id: string){
    const documentDialogRef = this.dialog.open(DocumentComponent, {
      data: { documentId: id}
    });
    documentDialogRef.afterClosed().subscribe( result => {
      this.getTechnicalEvaluation(this.technicalEvaluation.id);
      this.terminate();

    });
  }

  openPreviewDocumentDialog(src: string){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.width = '100%';
    dialogConfig.data = {src};
    const documentDialogRef = this.dialog.open(FilePreviewComponent, dialogConfig);

  }

  deleteDocument( id: string ) {

    this._document.deleteDocument( id )
            .subscribe ( () =>
            { this._document.getDocuments();
              this.snackService.warn('Se ha borrado correctamente el documento ');
              this.getTechnicalEvaluation(this.technicalEvaluation.id);
              this.terminate();

            });

  }

  createDocument(){
    const date = new Date();
    const document: Document = {id:'',name:"Evaluacion Tecnica",type: "", formId:this.technicalEvaluation.id,src:"./uploads",status:"active",createDate: date};
    this._document.createDocument(document)
      .subscribe(() =>
      {
        this._document.getDocuments();
        this.snackService.success('Se ha creado correctamente el documento ');
        this.getTechnicalEvaluation(this.technicalEvaluation.id);
      }
      );
  }

  /* sendEvent() {
    // console.log('eniva evento');
    this.eventRefresh.emit(true);
  } */


  changeFile(id: string) {
    this.dialog.open(ModalFileUploadComponent);
    this.modalUploadFileService.showModal( 'docs', id );

    this.modalUploadFileService.modalNotification
                .subscribe( (resp: any) => {
                  // console.log(resp);

                  this.changeIcon(resp.document.src);
                  this.getTechnicalEvaluation(this.technicalEvaluation.id);
                  // this.sendEvent();
                  this.terminate();
                });
  }

  changeIcon(src) {

    if (src === './uploads') {
      this.iconFile = '../assets/images/icon/add-file.png';
      return;
    }
    const ext = src.split('.');
    // console.log('extencion', ext);

    if (ext[1] === 'pdf') {
      this.iconFile = '../assets/images/icon/pdf.png';
    }

  }

}
