import { Component, OnInit } from '@angular/core';
import { AuthService } from '../services/auth.service';
import { User } from '../models/user';
import { UserService } from '../services/user.service';
import { Store } from '@ngrx/store';
import { AppState } from '../store/app.reducers';
import { resetStatus} from '../store/actions/status.actions';
import { routeMap } from '../config/global';
import { PositionService } from '../services/position/position.service';
import { OfficeService } from '../services/office/office.service';



@Component({
  selector: 'app-pages',
  templateUrl: './pages.component.html',
  styleUrls: ['./pages.component.scss']
})
export class PagesComponent implements OnInit {
  user: User;
  routeMap = routeMap;
  currentOffice = 'NONE';
  constructor(private authService: AuthService,
              private userService: UserService,
              private positionService: PositionService,
              private officeService: OfficeService,
              private store: Store<AppState>
              ) { }

  ngOnInit(): void {

    this.user = this.userService.user;
    // console.log('Usuario actual ', this.user)
    this.refresh();
  }

  logout(){
    this.store.dispatch( resetStatus());
    this.authService.logout();
  }

  refresh() {
    this.user = JSON.parse(this.authService.getUser());

    if (this.user) {
      this.positionService.getPosition(this.user.positionId).subscribe( myPosition => {
        this.officeService.getOffice(myPosition.officeId).subscribe( myOffice => {
          // console.log('mi officina es ', myOffice.name);

          if ( this.user.role === 'ADMIN_ROLE' ) {
            this.currentOffice = myOffice.name;
          } else {
            this.currentOffice = 'NONE';
          }

        });
      });
    }

  }
}
