import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { ProposalComparison } from 'src/app/models/proposalComparison.model';
import { GLOBAL } from '../../config/global';

@Injectable({
  providedIn: 'root'
})
export class ProposalComparisonService {
  private URL = GLOBAL.uri;
  constructor(private http: HttpClient) {}

  getProposalComparisons() {
    let url = this.URL + '/proposalComparisons';
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.proposalComparisons;
      })
    );
  }

  getProposalComparison(id: string) {
    let url = this.URL + '/proposalComparisons/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.proposalComparison;
      })
    );
  }

  getPCD(id: string) {
    let url = this.URL + '/proposalComparisons/all/' + `${id}`;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.proposalComparison;
      })
    );
  }

  createProposalComparison(proposalComparison: {code: string, description:string, provider:string}){
    let url = this.URL + '/proposalComparisons';
    return this.http.post( url, proposalComparison);

  }

  updateProposalComparison(proposalComparison: ProposalComparison){
    let url = this.URL + '/proposalComparisons/'+ proposalComparison.id;
    return this.http.put( url, proposalComparison);
  }

  deleteProposalComparison( id: string) {

    let url = this.URL + '/proposalComparisons/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }


}
