import { Component, OnInit } from '@angular/core';
import { TechnicalEvaluation } from 'src/app/models/technicalEvaluation.model';
import { TechnicalEvaluationService } from 'src/app/services/technicalEvaluation/technical-evaluation.service';
import { SnackService } from 'src/app/services/snack.service';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { AppState } from 'src/app/store/app.reducers';
import * as actions from '../../store/actions/status.actions';

@Component({
  selector: 'app-technical-evaluation',
  templateUrl: './technical-evaluation.component.html',
  styleUrls: ['./technical-evaluation.component.scss']
})
export class TechnicalEvaluationComponent implements OnInit {

  technicalEvaluations: TechnicalEvaluation[] = [];
  displayedColumns: string[] = ['number', 'date',  'to','edit', 'delete'];

  constructor(
    public _technicalEvaluation: TechnicalEvaluationService,
    public snackService: SnackService,
    private router: Router,
    private store: Store<AppState>
  ) { }

  ngOnInit(): void {
    this.getTechnicalEvaluations();
  }


  getTechnicalEvaluations(){
    this._technicalEvaluation.getTechnicalEvaluations()
      .subscribe(technicalEvaluations => this.technicalEvaluations = technicalEvaluations);
  }
  edit(id: string){
    this.store.dispatch( actions.changeFormId({ formId: id}) );
    this.router.navigateByUrl('/technicalEvaluation/edit');
  }

  delete( id: string ) {

    this._technicalEvaluation.deleteTechnicalEvaluation( id )
            .subscribe ( () =>
            {
              this.snackService.warn('Se ha borrado correctamente la Evaluacion Tecnica ');
              this.getTechnicalEvaluations();
            });

  }
}
