import { createReducer, on } from '@ngrx/store';
import { 
    loadStatus,
    loadStatusSuccess,
    loadStatusError,
    resetStatus,
    changeFormId,
    requestStatus,
    initialStatus,
    initialStatusSuccess,
    initialStatusError,
    createStatus,
    createStatusSuccess,
    createStatusError,
    updateStatus,
    updateStatusSuccess,
    updateStatusError
} from '../actions';
import { Status } from '../../models/status.model';



export interface StatusState {
    id: string,
    status : Status,
    loaded  : boolean,
    loading : boolean,
    error   : any
}

export const statusInitialState: StatusState = {
    id: null,
    status :  null,
    loaded  : false,
    loading : false,
    error   : null
}

const _statusReducer = createReducer(statusInitialState,

    on(loadStatus, (state, { id }) => ({
        ...state,
        loading: true,
        id: id
    })),

    on(loadStatusSuccess, (state, { status }) => ({
        ...state,
        loading: false,
        loaded: true,
        status: { ...status }
    })),
    on(loadStatusError, (state, { payload }) => ({
        ...state,
        loading: false,
        loaded: false,
        error: {
            url: payload.url,
            name: payload.name,
            message: payload.message
        }
    })),
    on(resetStatus, state => statusInitialState),

    on(requestStatus, (state, { userId, procedureId, name }) => ({
        ...state,
        loading: true,
        status: {
            id: null,
            procedureId,
            userId,
            formId: null,
            status: 'pendiente',
            dateAt: new Date(),
            current: true,
            codeRequest: null,
            name
         }
    })),

    on(changeFormId, (state, { formId }) => ({
        ...state,
        loading: true,
        status: {
            id: null,
            procedureId: null,
            userId: null,
            formId,
            status: 'Edit',
            dateAt: new Date(),
            current: true,
            codeRequest: null,
            name: null
         }
    })),

    on(initialStatus, (state, { status }) => ({
        ...state,
        loading: true,
        status
    })),

    on(initialStatusSuccess, (state, { status }) => ({
        ...state,
        loading: false,
        loaded: true,
        status: { ...status }
    })),
    on(initialStatusError, (state, { payload }) => ({
        ...state,
        loading: false,
        loaded: false,
        error: {
            url: payload.url,
            name: payload.name,
            message: payload.message
        }
    })),


    on(createStatus, (state, { status }) => ({
        ...state,
        loading: true,
        status
    })),

    on(createStatusSuccess, (state, { status }) => ({
        ...state,
        loading: false,
        loaded: true,
        status: { ...status }
    })),
    on(createStatusError, (state, { payload }) => ({
        ...state,
        loading: false,
        loaded: false,
        error: {
            url: payload.url,
            name: payload.name,
            message: payload.message
        }
    })),


    on(updateStatus, (state, { status }) => ({
        ...state,
        loading: true,
        status: { ...status }
    })),

    on(updateStatusSuccess, (state, { status }) => ({
        ...state,
        loading: false,
        loaded: true,
        status: { ...status }
    })),
    on(updateStatusError, (state, { payload }) => ({
        ...state,
        loading: false,
        loaded: false,
        error: {
            url: payload.url,
            name: payload.name,
            message: payload.message
        }
    })),
);

export function statusReducer(state, action) {
    return _statusReducer(state, action);
}
