import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import * as statusActions from '../actions/';
import { tap, mergeMap, map, concatMap, catchError, switchMap } from 'rxjs/operators';
import { StatusService } from '../../services/status/status.service';
import { of } from 'rxjs';
import { Status } from '../../models/status.model';
import { resetStatus } from '../actions/status.actions';



@Injectable()
export class StatusEffects {
    constructor(
        private actions$: Actions,
        private statusService: StatusService
    ) {}

    loadStatus$ = createEffect(
        () => this.actions$.pipe(
           ofType(statusActions.loadStatus),
        //    tap( data => console.log('effect tap ', data) ),
        switchMap(
               ( action ) => this.statusService.getStatus(action.id)
                    .pipe(
                        map( status => statusActions.loadStatusSuccess({ status })),
                        catchError( err => of(statusActions.loadStatusError({ payload: err })) )
                    )
           )
        )
    );

    initialStatus$ = createEffect(
        () => this.actions$.pipe(
           ofType(statusActions.initialStatus),
        //    tap( data => console.log('effect tap ', data) ),
        switchMap(
               ( action ) => this.statusService.createStatus(action.status)
                    .pipe(
                        map( (res: any) => statusActions.initialStatusSuccess({ status: res.status }) ),
                        catchError( err => of(statusActions.initialStatusError({ payload: err })) )
                    )
           )
        )
    );

    createStatus$ = createEffect(
        () => this.actions$.pipe(
           ofType(statusActions.createStatus),
        //    tap( data => console.log('effect tap ', data) ),
        concatMap(
               ( action ) => { return this.statusService.createStatus(action.status)
                    .pipe(
                        concatMap( (res: any) => [
                            statusActions.createStatusSuccess({ status: res.status }),
                            statusActions.resetStatus()
                        ]),
                        catchError( err => of(statusActions.createStatusError({ payload: err })) )
                    )}
           )
        )
    );

    editStatus$ = createEffect(
        () => this.actions$.pipe(
           ofType(statusActions.updateStatus),
        //    tap( data => console.log('effect tap ', data) ),
        switchMap(
               ( action ) => this.statusService.updateStatus(action.status)
                    .pipe(
                        map( (res: any) => statusActions.updateStatusSuccess({ status: res.status }) ),
                        catchError( err => of(statusActions.updateStatusError({ payload: err })) )
                    )
           )
        )
    );
}