import { Component, OnInit } from '@angular/core';
import { PurchaseOrder } from 'src/app/models/purchaseOrder.model';
import { PurchaseOrderService } from 'src/app/services/purchaseOrder/purchase-order.service';
import { SnackService } from 'src/app/services/snack.service';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { AppState } from 'src/app/store/app.reducers';
import * as actions from '../../store/actions/status.actions';

@Component({
  selector: 'app-purchase-order',
  templateUrl: './purchase-order.component.html',
  styleUrls: ['./purchase-order.component.scss']
})
export class PurchaseOrderComponent implements OnInit {

  purchaseOrders: PurchaseOrder[] = [];
  displayedColumns: string[] = ['code','description','edit','delete'];

  constructor(
    public _purchaseOrder: PurchaseOrderService,
    public snackService: SnackService,
    private router: Router,
    private store: Store<AppState>
  ) { }

  ngOnInit(): void {
    this.getPurchaseOrders();
  }

  getPurchaseOrders(){
    this._purchaseOrder.getPurchaseOrders()
      .subscribe(purchaseOrders => this.purchaseOrders = purchaseOrders);
  }

  edit(id: string){
    this.store.dispatch( actions.changeFormId({ formId: id}) );
    this.router.navigateByUrl('/purchaseOrder/edit');
  }

  delete(id: string){
    this._purchaseOrder.deletePurchaseOrder(id)
      .subscribe(() =>
      {
        this.snackService.warn('Se ha borrado correctamente la Orden de Compra ');
        this.getPurchaseOrders();
      })
  }

}
