import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { ProposalEvaluation } from 'src/app/models/proposalEvaluation.model';
import { GLOBAL } from '../../config/global';

@Injectable({
  providedIn: 'root'
})
export class ProposalEvaluationService {
  private URL = GLOBAL.uri;
  constructor(private http: HttpClient) { }

  getProposalEvaluations() {
    let url = this.URL + '/proposalEvaluations';
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.proposalEvaluations;
      })
    );
  }

  getProposalEvaluation(id: string) {
    let url = this.URL + '/proposalEvaluations/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.proposalEvaluation;
      })
    );
  }

  getPEP(id: string) {
    let url = this.URL + '/proposalEvaluations/all/' + `${id}`;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.proposalEvaluation;
      })
    );
  }


  createProposalEvaluation(proposalEvaluation: {code: string}){
    let url = this.URL + '/proposalEvaluations';
    return this.http.post( url, proposalEvaluation);

  }

  updateProposalEvaluation(proposalEvaluation: ProposalEvaluation){
    let url = this.URL + '/proposalEvaluations/'+ proposalEvaluation.id;
    return this.http.put( url, proposalEvaluation);
  }

  deleteProposalEvaluation( id: string) {

    let url = this.URL + '/proposalEvaluations/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }

}
