import { Component, OnInit, Inject } from '@angular/core';
import { ProposalProvider } from 'src/app/models/proposalProvider.model';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ProposalProviderService } from 'src/app/services/proposalProvider/proposal-provider.service';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SnackService } from 'src/app/services/snack.service';
import { Document } from 'src/app/models/document.model';
import { DocumentService } from 'src/app/services/document/document.service';

@Component({
  selector: 'app-proposal-provider-form',
  templateUrl: './proposal-provider-form.component.html',
  styleUrls: ['./proposal-provider-form.component.scss']
})
export class ProposalProviderFormComponent implements OnInit {

  proposalProvider: ProposalProvider;
  public proposalProviderForm: FormGroup;
  formId: string;
  proposalProviderId: string;

  constructor(
    public _proposalProvider: ProposalProviderService,
    private dialogRef: MatDialogRef<ProposalProviderFormComponent>,
    private proposalProviderFormBuilder: FormBuilder,
    @Inject(MAT_DIALOG_DATA) public data: ProposalProviderFormComponent,
    public snackService: SnackService,
    public _document: DocumentService,
  ) { }

  ngOnInit(): void {
    if(this.data.proposalProviderId !== 'new'){
      this.getProposalProvider(this.data.proposalProviderId);
    }

    this.proposalProviderForm = this.proposalProviderFormBuilder.group({
      name:[null, Validators.required],
      nit:[null, Validators.required],
      date:[null, Validators.required],
      formId:[this.data.formId],

    });
  }

  getProposalProvider(id: string){
    this._proposalProvider.getProposalProvider(id)
      .subscribe(proposalProvider => {
        if(!proposalProvider){
          this.onCloseDialog();
        }
        const {name, nit, date, formId} = proposalProvider;
        this.proposalProvider = proposalProvider;
        this.proposalProviderForm.setValue({name, nit, date, formId });
      })
  }

  onCloseDialog(){

    this.dialogRef.close();
  }
  save() {

    const {name} = this.proposalProviderForm.value;
    if ( this.proposalProvider ) {
      // update
      const data = {
        ...this.proposalProviderForm.value,
        id: this.proposalProvider.id,
        formId: this.proposalProvider.formId
      }
      console.log(data);
      this._proposalProvider.updateProposalProvider( data )
        .subscribe( resp => {
          this.snackService.success('Se ha actualizado correctamente el Proveedor ' + `${ name }`);
          this.onCloseDialog();
        })

    } else {
      // create

      this._proposalProvider.createProposalProvider( this.proposalProviderForm.value )
          .subscribe( (resp: any) => {
            this.snackService.success('Se ha creado correctamente el Proveedor '+ `${name }`);
            this.onCloseDialog();


        })
    }



  }

  delete( id: string ) {

    this._proposalProvider.deleteProposalProvider( id )
            .subscribe ( () =>
            { this._proposalProvider.getProposalProviders();
              this.snackService.warn('Se ha eliminado correctamente el Proveedor ');
            });

  }



}
