import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { PurchaseOrder } from 'src/app/models/purchaseOrder.model';
import { Document } from 'src/app/models/document.model';
import { PurchaseOrderService } from 'src/app/services/purchaseOrder/purchase-order.service';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { DocumentService } from 'src/app/services/document/document.service';
import { SnackService } from 'src/app/services/snack.service';
import { ModalFileUploadService } from 'src/app/components/modal-file-upload/modal-file-upload.service';
import { DocumentComponent } from '../document/document.component';
import { FilePreviewComponent } from '../file-preview/file-preview.component';
import { ModalFileUploadComponent } from 'src/app/components/modal-file-upload/modal-file-upload.component';
import { Status } from '../../models/status.model';
import { User } from '../../models/user';
import * as actions from '../../store/actions/status.actions';
import { AuthService } from '../../services/auth.service';
import { Store } from '@ngrx/store';
import { AppState } from '../../store/app.reducers';
import { Router } from '@angular/router';

@Component({
  selector: 'app-purchase-order-form',
  templateUrl: './purchase-order-form.component.html',
  styleUrls: ['./purchase-order-form.component.scss']
})
export class PurchaseOrderFormComponent implements OnInit {

  codeRequest: string;
  currentStatus: Status;
  currentUser: User;

  public purchaseOrderForm: FormGroup;
  public purchaseOrder: PurchaseOrder;
  documents: Document[] = [];
  displayedDocuments: string[] = ['document','preview','name', 'edit', 'delete'];
  iconFile = '../assets/images/icon/add-file.png';

  constructor(
    public _purchaseOrder: PurchaseOrderService,
    private authService: AuthService,
    private formBuilder: FormBuilder,
    private router: Router,
    private dialog: MatDialog,
    public _document: DocumentService,
    public snackService: SnackService,
    public modalUploadFileService: ModalFileUploadService,
    private store: Store<AppState>
  ) { }

  ngOnInit(): void {
    this.currentUser =  JSON.parse(this.authService.getUser()) ;
    this.purchaseOrderForm = this.formBuilder.group({
          code:[null, Validators.required],
          description:[null, Validators.required],

        });
    this.store.select('status').subscribe(({ status }) => {
      if (status != null) {
        // console.log('el estado actual es ', status);
        this.currentStatus = status;
        if ( status.formId != null) {
          this.getPurchaseOrder( status.formId);
        } else {
          this.getPurchaseOrder( 'new');
        }
      }
    });
  }

  getPurchaseOrder(id: string){

    if(id === 'new'){
      return;
    }
    this._purchaseOrder.getPOD(id)
      .subscribe(purchaseOrder => {
        if ( purchaseOrder ) {
          const {code, description} = purchaseOrder;
          this.purchaseOrder = purchaseOrder;
          this.documents = purchaseOrder.documents;
          this.purchaseOrderForm.setValue({code, description});
        }
      });
  }

  terminate() {
    if (this.currentStatus.procedureId) {
      const std: Status = Status.fromState( this.currentStatus );
      if (this.documents.length > 0) {
            std.userId = this.currentUser.id;
            std.status = 'terminado';
            // console.log('current status ', std);
            this.store.dispatch( actions.updateStatus({ status: std }));
          }else {
              std.status = 'pendiente';
          }
    }
  }

  save() {

    const {code} = this.purchaseOrderForm.value;

    if ( this.purchaseOrder ) {
      // update
      const data = {
        ...this.purchaseOrderForm.value,
        id: this.purchaseOrder.id
      }
      this._purchaseOrder.updatePurchaseOrder( data )
        .subscribe( resp => {
          this.snackService.success('Se ha actualizado correctamente la Orden de Compra ' + `${ code }`);
          if (!this.currentStatus.procedureId) {
            this.router.navigateByUrl('/purchaseOrders');
          } else {
            this.terminate();
          }
        })

    } else {
      // create

      this._purchaseOrder.createPurchaseOrder( this.purchaseOrderForm.value )
          .subscribe( (resp: any) => {
            if (this.currentStatus.procedureId) {
              const std: Status = Status.fromState( this.currentStatus );
              std.formId = resp.purchaseOrder.id;
              std.userId = this.currentUser.id;
              std.status = 'pendiente';
              // console.log('current status ', std);
              this.store.dispatch( actions.updateStatus({ status: std }));
            }
            this.getPurchaseOrder(resp.purchaseOrder.id);
            this.snackService.success('Se ha creado correctamente la Orden de Compra '+ `${code }`);

            // this.router.navigateByUrl(`/purchaseOrder/${ resp.purchaseOrder.id }`)
        });
    }



  }

  delete( id: string ) {
    this._purchaseOrder.deletePurchaseOrder( id )
            .subscribe ( () =>
            { this._purchaseOrder.getPurchaseOrders();
              this.snackService.warn('Se ha borrado correctamente la Orden Compra ');
            });
  }

  //document
  getDocuments(){
    this._document.getDocuments()
      .subscribe(documents => this.documents = documents);
  }
  openAddDocumentDialog(){
    const documentDialogRef = this.dialog.open(DocumentComponent, {
      data: { formId: this.purchaseOrder.id,
              documentId: 'new'
      }
    });
    documentDialogRef.afterClosed().subscribe( result => {

      this.getPurchaseOrder(this.purchaseOrder.id);
      //this.sendEvent();

    });
  }
  openUpdateDocumentDialog(id: string){
    const documentDialogRef = this.dialog.open(DocumentComponent, {
      data: { documentId: id}
    });
    documentDialogRef.afterClosed().subscribe( result => {
      this.getPurchaseOrder(this.purchaseOrder.id);
      console.log('Documentos cargados', this.documents);
      this.terminate();
    });
  }

  openPreviewDocumentDialog(src: string){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.width = '100%';
    dialogConfig.data = {src: src};
    const documentDialogRef = this.dialog.open(FilePreviewComponent, dialogConfig);

  }

  deleteDocument( id: string ) {

    this._document.deleteDocument( id )
            .subscribe ( () =>
            { this._document.getDocuments();
              this.snackService.warn('Se ha borrado correctamente el documento ');
              this.getPurchaseOrder(this.purchaseOrder.id);

              this.terminate();
            });
  }

  createDocument(){
    const document: Document = {id:'',name:"Orden Compra",type: "", formId:this.purchaseOrder.id,src:"./uploads", status:"active", createDate:new Date()};
    this._document.createDocument(document)
      .subscribe(() =>
        {
          this._document.getDocuments();
          this.snackService.success('Se ha creado correctamente el documento ');
          this.getPurchaseOrder(this.purchaseOrder.id);
        }
      );
  }

  changeFile(id: string) {
    this.dialog.open(ModalFileUploadComponent);
    this.modalUploadFileService.showModal( 'docs', id );

    this.modalUploadFileService.modalNotification
                .subscribe( (resp: any) => {
                  // console.log(resp);

                  this.changeIcon(resp.document.src);
                  this.getPurchaseOrder(this.purchaseOrder.id);
                  //this.sendEvent();
                  this.terminate();
                });
  }

  changeIcon(src) {

    if (src == "./uploads") {
      this.iconFile = '../assets/images/icon/add-file.png';
      return;
    }
    const ext = src.split('.');
    // console.log('extencion', ext);

    if (ext[1] === 'pdf') {
      this.iconFile = '../assets/images/icon/pdf.png';
    }

  }

}
