import { Component, OnInit } from '@angular/core';
import { BudgetCertification } from 'src/app/models/budgetCertification.model';
import { BudgetCertificationService } from 'src/app/services/budgetCertification/budget-certification.service';
import { SnackService } from 'src/app/services/snack.service';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { AppState } from '../../store/app.reducers';
import * as actions from '../../store/actions/status.actions';

@Component({
  selector: 'app-budget-certification',
  templateUrl: './budget-certification.component.html',
  styleUrls: ['./budget-certification.component.scss']
})
export class BudgetCertificationComponent implements OnInit {

  budgetCertifications: BudgetCertification[] = [];
  displayedColumns: string[] = ['reserveCode','edit','delete'];

  constructor(
    public _budgetCertification: BudgetCertificationService,
    public snackService: SnackService,
    private router: Router,
    private store: Store<AppState>,
  ) { }

  ngOnInit(): void {
    this.getBudgetCertifications();
  }

  getBudgetCertifications(){
    this._budgetCertification.getBudgetCertifications()
      .subscribe(budgetCertifications => this.budgetCertifications = budgetCertifications);
  }
  edit(id: string){
    this.store.dispatch( actions.changeFormId({ formId: id}) );
    this.router.navigateByUrl('/budgetCertification/edit');
  }
  delete( id: string ) {

    this._budgetCertification.deleteBudgetCertification( id )
            .subscribe ( () =>
            {
              this.snackService.warn('Se ha borrado correctamente la Certificacion Presupuestaria ');
              this.getBudgetCertifications();
            });

  }

}
