import { Component, OnInit } from '@angular/core';
import { TechnicalReport } from 'src/app/models/technicalReport.model';
import { TechnicalReportService } from 'src/app/services/technicalReport/technical-report.service';
import { SnackService } from 'src/app/services/snack.service';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { AppState } from 'src/app/store/app.reducers';
import * as actions from '../../store/actions/status.actions';

@Component({
  selector: 'app-technical-report',
  templateUrl: './technical-report.component.html',
  styleUrls: ['./technical-report.component.scss']
})
export class TechnicalReportComponent implements OnInit {

  technicalReports: TechnicalReport[] = [];
  displayedColumns: string[] = ['background', 'acquisitionObjetive', 'testInspection', 'supportDocument','edit', 'delete'];
  constructor(
    public _technicalReport: TechnicalReportService,
    public snackService: SnackService,
    private router: Router,
    private store: Store<AppState>
  ) { }

  ngOnInit(): void {
    this.getTechnicalReports();
  }


  getTechnicalReports(){
    this._technicalReport.getTechnicalReports()
      .subscribe(technicalReports => this.technicalReports = technicalReports);
  }

  edit(id: string){
    this.store.dispatch( actions.changeFormId({ formId: id}) );
    this.router.navigateByUrl('/technicalReport/edit');
  }

  delete( id: string ) {

    this._technicalReport.deleteTechnicalReport( id )
            .subscribe ( () =>
            {
              this.snackService.warn('Se ha borrado correctamente la Especificacion Tecnica ');
              this.getTechnicalReports();
            });

  }
}
