import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { Product } from 'src/app/models/product.model';
import { GLOBAL } from '../../config/global';

@Injectable({
  providedIn: 'root'
})
export class ProductService {
  private URL = GLOBAL.uri;
  constructor(private http: HttpClient) {}

  getProducts() {
    let url = this.URL + '/products';
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.products;
      })
    );
  }

  getProduct(id: string) {
    let url = this.URL + '/products/' + id;
    return this.http.get(url).pipe(
      map((resp: any) => {
        return resp.product;
      })
    );
  }

  createProduct(product: {name: string, description:string, quantity: string, price: number, requestId: string}){
    let url = this.URL + '/products';
    return this.http.post( url, product);

  }

  updateProduct(product: Product){
    let url = this.URL + '/products/'+ product.id;
    return this.http.put( url, product);
  }

  deleteProduct( id: string) {

    let url = this.URL + '/products/' + id;
    return this.http.delete( url ).pipe(
      map( resp => {
        return resp;
      })
    );
  }


}
